//Styles for page job detail
@use "vars" as *;
.job-detail{
	.quote-list{
		&__ttl{
			margin-bottom: 26px;
			@include mq(){
				margin-bottom: 18px;
			}
		}
		&__date-ttl{
			small{
				margin-left: 20px;
				@include mq(){
					margin-left: 10px;
				}
			}
		}
		&__date--02{
			margin-bottom: 30px;
			@include mq(){
				&:last-child{
					margin-bottom: 0;
				}
			}
		}
	}
}


.report-today{
	.quote-list__fee{
		margin-bottom: 21px;
		&:last-of-type{
			margin-bottom: 0;
		}
	}
}