@use "vars" as *;

.quote-list--026{
	margin-bottom: 33px;
	.request-form{
		&__ct{
			&-wrap{
				@include flex(end, 0);
				position: relative;
				.request-form__ct-right{
					width: 100%;
				}
			}
		}
	}
	.quote-list{
		&__fee{
			&-input{
				margin-right: 14px;
				&:last-child{
					margin-right: 0;
				}
				&.w331{
					@include mq(md-min){
						width: 331px;
					}
				}
				&.w146{
					@include mq(md-min){
						width: 146px;
						.form-edit__input{
							width: 93px;
						}
					}
				}
				&.w186{
					@include mq(md-min){
						width: 186px;
					}
				}
				&.w161{
					@include mq(md-min){
						width: 161px;
					}
				}
				&.w226{
					width: 226px;
					display: inline-flex;
					@include mq{
						margin-bottom: 15px;
						width: 100%;
						input{
							width: calc(100% - 109px);
						}
					}
				}
				& > span{
					min-width: 43px;
				}
				&.w133{
					@include mq(md-min){
						width: 133px;
						input{
							width: calc(100% - 43px);
						}
					}
				}
			}
			&-ct{
				padding-right: 0;
				@include mq(md-min){
					width: calc(100% - 178px);
				}
			}
		}
		&__item{
			margin-bottom: 18px;
			padding-bottom: 17px;
			&.mbpc-44 {
				margin-bottom: 50px;
			}
			
			&.mbpc-30{
				margin-bottom: 30px;
			}
		}
	}
	.form-edit__radio input[type=radio] + span{
		min-width: 105px;
	}
	.quote-list__date-item {
		&:first-child {
			@include mq{
				width: 100%;
				max-width: 100%;
			}
		}
	}
	.quote-list__fee-ttl{
		@include mq(md-min){
			width: 178px;
		}
		padding-left: 0;
	}
}