@use "vars" as *;

.sec-application{
	.l-content{
		padding-bottom: 13px;
		@include mq{
			padding-bottom: 0;
		}
	}
}
.application{
	&-list{
		@include flex(0, start, wrap);
	}
	&-item{
		width: calc(33.33% - 14px);
		margin-right: 21px;
		margin-bottom: 21px;
		border: 1px solid #F2F0EE;
		border-radius: 4px;
		padding: 20px 20px 28px;
		&:last-child, &:nth-child(3n){
			margin-right: 0;
		}
		@include mq(){
			width: 100%;
			margin-right: 0;
			padding: 16px 18px 28px;
			margin-bottom: 19px;
			&:last-child{
				margin-bottom: 0;
			}
		}
		&__ttl{
			@include fts(18, 27);
			font-weight: 700;
			color: #5BC0DE;
			margin-bottom: 7px;
			text-align: center;
			white-space: nowrap;
			@include mq(md-min){
				width: calc(100% + 20px);
				margin: 0 -10px 0;
			}
		}
		&__ico{
			margin-bottom: 13px;
			text-align: center;
			display: block;
			@include mq{
				margin-bottom: 17px;
			}
			img{
				width: 57px;
			}
		}
		&__link{
			padding: 16px 12px;
			background: #F2F0EE;
			border-radius: 4px;
			@include fts(15, 15);
			letter-spacing: 0.05em;
			font-weight: 700;
			margin-bottom: 19px;
			display: inline-block;
			width: 100%;
			position: relative;
			&:last-child{
				margin-bottom: 0;
			}
			@include mq{
				padding: 17px 12px;
				margin-bottom: 15px;
			}
			&:hover{
				opacity: 0.75;
				
			}
			&:after{
				content: '';
				width: 6px;
				height: 6px;
				border-top: 2px solid #464646;
				border-right: 2px solid #464646;
				display: inline-block;
				transform: rotate(45deg) translateY(-50%);
				position: absolute;
				top: calc(50%);
				right: 11px;
				@include mq{
					right: 16px;
				}
			}
		}
	}
}