@use "vars" as *;
.request-box{
	margin-bottom: 38px;
	@include mq(){
		margin-bottom: 10px;
	}
}

.request-form{
	margin-top: 30px;
    //padding-top: 18px;
	margin-bottom: 30px;
	border-top: 1px solid #E0E1E3;
	@include mq(){
		margin-top: 30px;
		margin-bottom: 25px;
		border-top: 0;
		//padding: 14px 0px 0;
	}
	&__item{
		@include flex();
		//margin-bottom: 22px;
		padding-top: 15px;
		padding-bottom: 15px;
		line-height: 1.642;
		border-bottom: 1px solid #E0E1E3;
		@include mq(){
			//margin-bottom: 14px;
			@include flex(0,0,wrap);
		}
		&:last-child{
			border-bottom: 0;
		}
	}
	&--02{
		@include mq(md-min){
			.request-form__ttl{
				width: 151px;
			}
			.request-form__ct{
				width: calc(100% - 151px);
			}
		}
	}
	&__ttl{
		width: 111px;
		padding-left: 8px;
		font-weight: bold;
		color: #464646;
		padding-top: 12px;
		@include font-size(14);
		position: relative;
		@include mq(){
			width: 100%;
			padding-top: 0;
			padding-left:0;
			margin-bottom: 8px;
		}
		span{
			color: #F04850;
			font-weight: 500;
			margin-left: 9px;
			@include mq(){
				margin-left: 3px;
			}
		}
		&-request{
			position: absolute;
			top: 0px;
			right: 5px;
		}
		.disclosure{
			display: block;
			margin: 0;
			color: #5BC0DE;
			font-size: 85.71%;
			line-height: 1.2;
			@include mq(){
				margin-left: auto;
			}
			&.--off{
				color: #969696;
			}
		}
		&.pt0{
			padding-top: 0;
		}
		&.w100{
			width: 100%;
		}
		&--lg{
			@include font-size(16);
		}
	}
	&__ct{
		width: calc(100% - 111px);
		padding-right: 15px;
		font-weight: 500;
		@include font-size(14);
		@include mq(){
			width: 100%;
			padding-right: 0;
		}
		&-right{
			text-align: right;
		}
		&--zip{
			@include flex(0,center);
			@include mq(){
				@include flex(0,0,wrap);
			}
			.form-edit__input{
				width: 192px;
				@include mq(){
					width: 123px;
					margin-bottom: 10px;
				}
			}
			p{
				width: calc(100% - 192px);
				padding-left: 12px;
				color: #969696;
				font-weight: 500;
				@include font-size(13);
				@include mq(){
					width: 100%;
					padding-left: 0;
				}
			}
		}
	}
	&--confirm{
		margin-top: 0;
		//padding-top: 0;
		//border-top: 0;
		.request-form__ttl{
			padding-top: 0;
		}
	}
	&.request-form--180{
		.request-form__ttl{
			@include mq(md-min){
				width: 180px;
			}
		}
		.request-form__ct{
			@include mq(md-min){
				width: calc(100% - 180px);
			}
		}
	}
}

.request-note{
	padding: 19px 30px;
    margin-bottom: 30px;
    line-height: 1.75;
	background: rgba(#5BC0DE,0.15);
	color: #464646;
	font-weight: 500;
	@include font-size(14);
	@include mq(){
		padding: 15px 24px 20px;
	}
}

.request-btn{
	@include flex(center);
	&__item{

	}
}

