@use "vars" as *;

#page-P093{
	padding-bottom: 31px;
	@include mq(){
		padding-bottom: 0;
	}
}
.sec-card{
	.l-content{
		@include mq(){
			padding-top: 0;
		}
	}
}
.card-block{
	max-width: 558px;
	margin: 10px auto;
	@include mq(sm){
		margin: 0 -20px;
		max-width: 640px;
	}
	&__img{
		img{
			width: 100%;
			vertical-align: top;
		}
	}
	&__ct{
		background: #5BC0DE;
		color: #fff;
		padding: 16px 10px 8px 10px;
		margin-bottom: 17px;
		@include mq(){
			padding: 10px 10px 6px 10px;
			margin-bottom: 7px;
		}
	}
	&__txt{
		@include fts(19,30);
		font-weight: 700;
		margin-bottom: 7px;
		@include mq(){
			@include font-size(14);
		}
	}
	&__ttl{
		font-weight: 700;
		span{
			@include fts(28,30);
			display: block;
			margin-bottom: -7px;
			@include mq(){
				@include fts(20,20);
				margin-bottom: -3px;
			}
		}
		small{
			@include fts(14,14);
		}
	}
	&__tag{
		@include flex(center,0,wrap);
		margin-bottom: 44px;
		@include mq(){
			margin-bottom: 21px;
		}
		span{
			background: #5BC0DE;
			padding: 1px 13px 0 13px;
			border-radius: 4px;
			color: #fff;
			margin: 0 7px;
			@include mq(){
				padding: 1px 10px 0 10px;
				margin: 0 5px;
			}
			@include mq(xs){
				margin-bottom: 5px;
			}
		}
	}
	p{
		color: #464646;
		line-height: 1.714;
		font-weight: 300;
		margin-bottom: 14px;
		@include mq(){
			margin-bottom: 7px;
		}
		a{
			color: #464646;
			font-weight: 300;
			text-decoration: none;
		}
	}
}