@use "vars" as *;

.page-schedule{

}

.register-schedule{
	margin-bottom: 40px;
	&__ttl{
		margin-bottom: 20px;
	}
	&__txt-intro{
		margin-bottom: 25px;
		@include mq(){
			margin-bottom: 15px;
		}
	}
	
	&__month{
		background: #F2F0EE;
		padding: 15px 0;
		margin-bottom: 17px;
		@include mq(){
			margin: 0 calc(((100vw - 100%) / 2) * -1) 15px;
		}
		&-wp{
			max-width: 260px;
			width: 100%;
			margin: 0 auto;
			position: relative;
			@include mq(){
				max-width: 225px;
			}
		}
		&-ct{
			@include font-size(20);
			font-weight: 700;
			text-align: center;
		}
		&-control{
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			@include font-size(13);
			font-weight: 700;
			color: #000;
			&--prev{
				left: 0;
			}
			&--next{
				right: 0;
			}
		}
	}
	
	&__heading{
		@include flex(between, 0, wrap);
		margin-bottom: 10px;
		@include mq(){
			margin-bottom: 13px;
		}
		@include mq(sm){
			display: block;
		}
		&-txt{
			margin-bottom: 10px;
			@include mq(sm){
				margin-bottom: 15px;
			}
		}
		&-btn{
			@include flex(end, center, wrap);
			margin-bottom: 10px;
			@include mq(){
				margin-right: -5px;
				margin-bottom: 0;
			}
			li{
				margin-left: 10px;
				&:first-of-type{
					margin-left: 0;
				}
			}
		}
	}
	
	.questions-box{
		@include mq(){
			padding: 0;
		}
	}
}

.appointment-schedule{
	&__ttl{
		padding-bottom: 30px;
		margin-bottom: 16px;
		border-bottom: 1px solid #E0E1E3;
	}
	&__intro{
		position: relative;
		margin-bottom: 23px;
		@include mq(){
			margin-bottom: 13px;
		}
		&-txt-lead{
			margin-bottom: 10px;
		}
		&-note{
			@include fts(13, 20);
			font-weight: 500;
			color: #969696;
			@include mq(){
				padding-bottom: 30px;
			}
		}
		&-btn{
			max-width: 132px;
			position: absolute;
			right: 0;
			bottom: 0;
		}
	}
	
	.questions-box{
		@include mq(){
			padding: 0;
		}
	}
}

//Styles modules table schedule
.m-schedule__table{
	margin-bottom: 20px;
	@include mq(){
		margin: 0 -5px 20px;
	}
	table{
		width: 100%;
		border: 1px solid #E0E1E3;
		tr{
			th, td{
				width: calc(100%/7);
				border-right: 1px solid #E0E1E3;
				border-bottom: 1px solid #E0E1E3;
			}
			th{
				@include font-size(13);
				font-weight: 700;
				text-align: center;
				height: 40px;
				background: #F2F0EE;
				@include mq(){
					height: 27px;
				}
				&.sunday{
					background: #F6D5D0;
				}
				&.monday{
					background: rgba(#5BC0DE, 0.41);
				}
			}
			td{
				min-height: 120px;
				text-align: center;
				vertical-align: top;
				padding: 6px 0;
				cursor: pointer;
				@include mq(){
					padding: 3px 0;
				}
				&:hover{
					background: #F6CF5A;
					opacity: 1;
				}
				&.event{
					background: rgba(#F6CF5A, 0.5);
				}
				//&.td-choose{
				//	background: #F6CF5A;
				//	
				//}
				&.td-gray{
					background: rgba(#F2F0EE, 0.4);
				}
			}
		}
	}
	
	&-day{
		@include font-size(16);
		font-weight: 700;
		text-align: center;
		margin-bottom: 14px;
		@include mq(){
			margin-bottom: 8px;
			@include font-size(14);
		}
	}
	&-time{
		position: relative;
		z-index: 2;
		li{
			position: relative;
			@include font-size(12);
			font-weight: 500;
			display: inline-block;
			padding-left: 15px;
			margin-bottom: 10px;
			line-break: anywhere;
			@include mq(){
				@include font-size(10);
				line-height: 1.3;
				margin-bottom: 3px;
			}
			&:last-of-type{
				margin-bottom: 0;
			}
			@include p-before(13px, 13px){
				border: 2px solid #F6D5D0;
				background: #fff;
				border-radius: 50%;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				@include mq(){
					left: 2px;
				}
			}
			span{
				&:last-of-type{
					@include mq(sm){
						display: block;
					}
				}
			}
		}
	}
	&-empty{
		text-align: center;
		@include font-size(13);
		font-weight: 500;
		color: #5BC0DE;
		position: relative;
		top: 10px;
		@include mq(){
			@include font-size(12);
		}
	}
	&-close{
		display: inline-block;
		width: 38px;
		margin: 0 auto;
	}
	&-booked{
		@include fts(13, 15);
		font-weight: 700;
		color: #969696;
		width: 38px;
		height: 38px;
		border-radius: 3px;
		background: #E0E1E3;
		@include flex(center, center);
		display: inline-flex;
		@include mq{
			width: 35px;
			height: 35px;
		}
	}
	
	&--styles02{
		table{
			tr{
				td{
					vertical-align: middle;
					padding: 20px 0;
					@include mq(){
						padding: 11px 0;
					}
					.m-schedule__table-empty{
						top: 0;
					}
				}
			}
		}
		
	}
	&-fl{
		@include flex(center, start);
		height: 71px;
		@include mq{
			height: 50px;
		}
	}
}
//End Styles modules table schedule