@use "vars" as *;

.money-amount{
	margin-bottom: 24px;
	&__wp{
		border: 5px solid #F2F0EE;
		border-radius: 4px;
		padding: 15px 20px 12px;
		margin-bottom: 12px;
		@include mq(){
			padding: 22px 15px 23px;
		}
	}
	&__flex{
		@include flex(between, center, wrap);
		padding-bottom: 22px;
		margin-bottom: 13px;
		border-bottom: 1px solid #E0E1E3;
		@include mq(sm){
			display: block;
		}
	}
	&__label{
		@include font-size(14.5);
		font-weight: 700;
		@include mq(sm){
			width: 100%;
			text-align: center;
			margin-bottom: 17px;
		}
	}
	&__price{
		@include font-size(30);
		font-weight: 700;
		color: #F04850;
		@include mq(sm){
			width: 100%;
			text-align: center;
		}
	}
	&__btn{
		text-align: center;
		a{
			@include font-size(12);
			font-weight: 500;
			&:after{
				color: #5BC0DE;
				@include ft-awesome(f054);
				margin-left: 10px;
			}
		}
	}
	
	&__note{
		text-align: right;
		@include font-size(12);
		font-weight: 500;
		color: #969696;
	}
}


.request-month{
	margin-bottom: 30px;
	&__ttl{
		margin-bottom: 15px;
		@include mq(){
			@include font-size(14.5);
			margin-bottom: 20px;
		}
	}
	&__dashboard{
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		gap: 0 21px;
		@include mq(){
			gap: 0 17px;
		}
		&-item{
			border: 1px solid #E0E1E3;
			border-radius: 4px;
			padding: 20px 20px 17px;
			text-align: center;
			@include mq(){
				padding: 15px 0 10px;
			}
		}
		&-ttl{
			text-align: center;
			@include font-size(16);
			font-weight: 700;
			margin-bottom: 14px;
			display: block;
		}
		&-icn{
			margin-bottom: 14px;
			height: 44px;
			@include flex(center, center);
			@include mq(){
				width: 30px;
				height: 38px;
				margin: 0 auto;
			}
		}
		&-number{
			@include font-size(18);
			font-weight: 700;
			padding-bottom: 20px;
			margin-bottom: 15px;
			border-bottom: 1px solid #ccc;
			@include mq(){
				margin-right: 3px;
				position: relative;
				bottom: -6px;
				padding-bottom: 18px;
				margin-bottom: 15px;
			}
			span{
				@include font-size(35);
				font-weight: 500;
				color: #5BC0DE;
				margin-right: 12px;
				position: relative;
				bottom: -6px;
				@include mq(){
					margin-right: 3px;
				}
			}
		}
		&-link{
			@include font-size(12);
			font-weight: 500;
			&:after{
				color: #5BC0DE;
				@include ft-awesome(f054);
				margin-left: 12px;
				@include mq(){
					margin-left: 5px;
				}
			}
		}
	}
}


.achievement-report{
	@include font-size(14.5);
	&__ttl{
		margin-bottom: 13px;
		@include mq(){
			@include font-size(14.5);
		}
	}
	&__item{
		@include flex(between, 0, wrap);
		padding: 18px 20px;
		background: #FAFAFA;
		border-bottom: 1px solid #E0E1E3;
		@include mq(){
			padding: 18px 12px;
		}
		&:first-of-type{
			border-top: 1px solid #E0E1E3;
		}
		&:nth-child(even){
			background: #FFF;
		}
	}
	&__label{
		font-weight: 700;
		.statuschips{
			margin-left: 5px;
		}
	}
	&__ct{
		font-weight: 500;
		padding-right: 11px;
		@include mq(){
			padding-right: 0;
		}
	}
}