@use "vars" as *;

/* ====================================================
Layout
==================================================== */
.wrapper {
	padding-bottom: 40px;
	overflow-x: hidden;
	@include mq(md-min) {
		padding-top: 80px;
		padding-bottom: 80px;
		min-height: calc(100vh - 80px);
	}
	@include mq() {
		background: #fff;
	}
}

.inner{
	width: 100%;
	margin: 0 auto;
	padding: 0 20px;
	max-width: calc(1000px + 40px);
	@include mq() {
		width: 100%;
		padding: 0 15px;
	}
	&--1150{
		max-width: calc(1150px + 40px);
	}
	&--1300{
		max-width: calc(1300px + 40px);
	}
}

.vw100-sp{
	@include mq(){
		margin-left: calc(((100vw - 100%) / 2) * -1);
		margin-right: calc(((100vw - 100%) / 2) * -1);
		width: 100vw;
	}
}


.breadcrumb {
	@include font-size(13);
	
	.inner {
		@include flex(start, center);
		padding-top: 20px;
		padding-bottom: 20px;
		//overflow-x: scroll;
		@include mq() {
			padding-top: 1em;
			padding-bottom: 1em;
			background: #fff;
		}
	}
	
	a {
		position: relative;
		display: inline-block;
		margin-right: 3em;
		color: #969696;
		white-space: nowrap;
		@include mq() {
			margin-right: 2em;
		}
		
		&:before {
			position: absolute;
			content: ">";
			right: -1.5em;
			@include v-align;
			margin-top: -2px;
			font-size: 130%;
			@include mq() {
				right: -1.1em;
			}
		}
	}
	
	.current {
		font-weight: bold;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
}


.l-content {
	padding: 30px 20px 50px;
	background: #fff;
	border-radius: 4px;
	@include mq() {
		margin: 0 calc(((100vw - 100%) / 2) * -1);
		padding: 30px 20px;
		width: 100vw;
	}
	
	//Modifier l-content block  for P005
	&--styles02 {
		padding: 43px 20px 75px;
		@include mq() {
			padding: 24px 20px;
		}
	}
	
	//End Modifier l-content block  for P005
	&__body {
		margin: 0 auto;
		max-width: 780px;
	}
	&__01{
		@include flex(between,start,wrap);
		//grid-template-columns: 300px 1fr;
		gap: 30px 25px;
		flex-direction: row-reverse;
		@include mq() {
			grid-template-columns: 1fr;
			gap: 0;
		}
		
		&-l {
			padding: 30px 20px;
			background: #fff;
			border-radius: 4px;
			@include mq(md-min) {
				width: calc(100% - 325px);
				//order: 2;
			}
			@include mq() {
				margin: 0 calc(((100vw - 100%) / 2) * -1);
				padding: 20px 20px 0;
				width: 100vw;
			}
		}
		
		&-r {
			padding: 30px 20px;
			width: 100%;
			background: #fff;
			border-radius: 4px;
			@include mq(md-min) {
				width: 300px;
				//order: 1;
			}
			@include mq() {
				padding: 25px 0;
				background: transparent;
			}
			&.sp-gray{
				@include mq(){
					background: #FAFAFA;
					width: 100vw;
					padding: 25px 20px;
					margin: 0 calc((100vw - 100%) / 2 * -1);
				}
			}
		}
	}
	
	&__02 {
		display: grid;
		grid-template-columns: 1fr 300px;
		gap: 30px 20px;
		padding: 30px 20px 45px;
		background: #fff;
		@include mq() {
			grid-template-columns: 1fr;
			margin: 0 calc(((100vw - 100%) / 2) * -1);
			padding: 40px 20px;
			width: 100vw;
		}
		//&-l{
		//	overflow: hidden;
		//}
		//Modifier l-content block  for P006
		&--styles02 {
			padding: 47px 20px;
			@include mq() {
				padding: 0 20px 24px;
			}
			
			.l-content__02 {
				&-l {
					@include mq() {
						order: 2;
					}
				}
				
				&-r {
					@include mq() {
						order: 1;
					}
				}
			}
		}
		
		//End Modifier l-content block  for P006
	}
	
	&__03 {
		display: grid;
		align-items: start;
		grid-template-columns: 300px 1fr;
		gap: 30px 20px;
		padding: 30px 20px 70px;
		background: #fff;
		@include mq() {
			grid-template-columns: 1fr;
			margin: 0 calc(((100vw - 100%) / 2) * -1);
			padding: 40px 12px 30px 12px;
			width: 100vw;
		}
		
		&-l {
			padding: 22px 22px 0 22px;
			border: 1px #E0E1E3 solid;
			border-radius: 4px;
			@include mq() {
				padding: 0;
				border-width: 0 0 1px 0;
			}
			&--noborder{
				border: none;
				padding: 0;
			}
		}
		
		&-r {
			padding-top: 4px;
		}
	}
}
.pd-0--sp{
	@include mq() {
		padding-top: 0;
	}
}



.m-tabs {
	@include flex(center, center);
	margin-bottom: 47px;
	@include mq() {
		margin-bottom: 21px;
	}
	
	&__item {
		//padding: .55em .8em;
		padding: 0.55em 1.6em;
		@include font-size(13);
		font-weight: bold;
		text-align: center;
		background: #fff;
		border-top: 1px solid #E0E1E3;
		border-bottom: 1px solid #E0E1E3;
		cursor: pointer;
		@include mq{
			padding: 0.55em 0.6em;
		}
		&:first-child {
			border-left: 1px solid #E0E1E3;
			border-radius: 4px 0 0 4px;
		}
		
		&:last-child {
			border-right: 1px solid #E0E1E3;
			border-radius: 0 4px 4px 0
		}
		
		&:not(:first-child) {
			border-left: 1px solid #E0E1E3;
		}
		
		&.is-active {
			color: #fff;
			background: #68D0EF
		}
		
		//&:hover{
		//	@media (any-hover: hover) {
		//		color: #fff;
		//		background: #68D0EF
		//	}
		//}
	}
	
	&02 {
		@include flex(center, center);
		margin-bottom: 30px;
		@include mq() {
			margin: 0 calc(((100vw - 100%) / 2) * -1) 30px;
			width: 100vw;
		}
		
		&__item {
			@include flex(center, center);
			gap: 0 12px;
			position: relative;
			flex-grow: 1;
			padding: .55em 1em .85em;
			text-align: center;
			@include font-size(15);
			font-weight: bold;
			background: #fff;
			border-top: 1px solid #E0E1E3;
			border-left: 1px solid #E0E1E3;
			cursor: pointer;
			transition: .2s;
			@include mq() {
				> span {
					display: none;
				}
			}
			@include p-before(100%, 4px) {
				bottom: 0;
				left: 0;
				background: #F2F0EE;
			}
			
			&:last-child {
				border-right: 1px solid #E0E1E3;
			}
			
			&:hover:not(.is-active) {
				@media (any-hover: hover) {
					opacity: .7;
				}
			}
			
			&-icn--on {
				display: none;
			}
			
			&.is-active {
				color: #fff;
				background: #5BC0DE;
				
				&:before {
					background: #5BC0DE;
				}
				
				@include p-after(0, 0) {
					bottom: -9px;
					left: 0;
					right: 0;
					margin: 0 auto;
					border-style: solid;
					border-width: 10px 6px 0 6px;
					border-color: #5BC0DE transparent transparent transparent;
				}
				
				.m-tabs02__item-icn--off {
					display: none;
				}
				
				.m-tabs02__item-icn--on {
					display: block;
				}
			}
		}
	}
	&--mb{
		margin-bottom: 20px;
		@include mq(){
			margin-bottom: 14px;
		}
	}
}

[data-tab-content] {
	display: none
}

[data-tab-content].is-active {
	display: block;
}


.user-status {
	@include flex(start, center);
	
	&__medal {
		> span {
			display: inline-block;
			width: 20px;
			height: 20px;
			background-repeat: no-repeat;
			background-size: contain;
			background-position: left center;
		}
		
		&--gold {
			background-image: url(../img/common/icn_medal_gold.png);
		}
		
		&--silver {
			background-image: url(../img/common/icn_medal_silver.png);
		}
		
		&--bronze {
			background-image: url(../img/common/icn_medal_bronze.png);
		}
		
		&--regular {
			background-image: url(../img/common/icn_medal_regular.png);
		}
		
		&--no {
			background-image: url(../img/common/icn_medal_no.png);
		}
	}
	
	
	&__star-icn {
		position: relative;
		display: block;
		&:before,
		&:after{
			position: absolute;
			top: 0;
			left: 0;
			content: "";
			height: 100%;
		}
		&:before{
			width: 100%;
			background: url(../img/common/icn_star_gray.png) no-repeat left center;
			background-size: contain;
		}
		&:after{
			background: url(../img/common/icn_star.png) no-repeat left center;
		}
		&[data-rate="0"]:after{
			width: 0%;
		}
		&[data-rate="0.5"]:after{
			width: 8%;
		}
		&[data-rate="1"]:after{
			width: 16%;
		}
		&[data-rate="1.5"]:after{
			width: 29%;
		}
		&[data-rate="2"]:after{
			width: 40%;
		}
		&[data-rate="2.5"]:after{
			width: 49%;
		}
		&[data-rate="3"]:after{
			width: 60%;
		}
		&[data-rate="3.5"]:after{
			width: 70%;
		}
		&[data-rate="4"]:after{
			width: 83%;
		}
		&[data-rate="4.5"]:after{
			width: 90%;
		}
		&[data-rate="5"]:after{
			width: 100%;
		}
	}
	&__num{
		@include font-size(12);
	}
}


.user-heading {
	&__image {
		margin: 0 auto 15px;
		width: 100px;
		height: 100px;
		border-radius: 50%;
		overflow: hidden;
	}
	
	&__name {
		margin-bottom: 5px;
		@include font-size(25);
		font-weight: bold;
		text-align: center;
	}
	
	&__add {
		@include flex(center, start, wrap);
		font-weight: 500;
		margin-bottom: 14px;
		
		img {
			position: relative;
			top: 4px;
			margin-right: 5px;
			width: 11px;
		}
		
		&-txt {
			display: inline-block;
			max-width: calc(100% - 16px);
		}
	}
	.user-status{
		justify-content: center;
		&__star > span{
			width: 120px;
			height: 20px;
			&:after{
				background-size: auto 20px;
			}
		}
		&__medal > span{
			width: 28px;
			height: 30px;
		}
	}
	&--02{
		border-bottom: 1px #E0E1E3 solid;
		padding-bottom: 20px;
		margin-bottom: 10px;
		.user-heading{
			&__image{
				width: 150px;
				height: 150px;
				margin-bottom: 21px;
			}
			&__name{
				@include font-size(20);
				margin-bottom: 7px;
			}
		}
		.c-address{
			margin-bottom: 10px;
		}
	}
	&--03{
		.user-heading{
			&__image{
				width: 77px;
				height: 77px;
				@include mq(sm){
					width: 127px;
					height: 127px;
				}
			}
		}
		.user-status{
			margin-bottom: 10px;
		}
	}
	&--04{
		@include flex(0,center,wrap);
		height: 24px;
		.user-status{
			margin-right: 13px;
			@include mq(){
				margin-right: 12px;
			}
			&__star {
				> span{
					width: 76px;
					height: 13px;
					&:before{
						background-size: cover;
					}
					&:after{
						background-size: cover;
					}
				}
			}
			&--02{
				.user-status{
					&__star{
						> span{
							width: 180px;
							height: 4px;
							border-radius: 4px;
							@include mq(sm){
								width: 90px;
							}
							@include mq(xs){
								width: 40px;
							}
							&:before{
								background: #F2F0EE;
								border-radius: 4px;
							}
							&:after{
								background: #FECC00;
								border-radius: 4px;
							}
						}
					}
				}
			}
		}
		.user-txt{
			font-weight: 500;
			color: #969696;
		}
	}
}

.nav-scroll{
	overflow-x: auto;
	//padding-bottom: 10px;
	@include mq(){
		margin: 0 calc(((100vw - 100%) / 2) * -1);
		width: 100vw;
		.m-subnav{
			//width: 800px;
			margin-bottom: 0;
		}
	}
}

.m-subnav{
	@include flex(start,end);
	margin-bottom: 40px;
	border-bottom: 4px solid #F2F0EE;
	@include mq() {
		justify-content: space-between;
		margin: 0 calc(((100vw - 100%) / 2) * -1) 40px;
		// width: 100vw;
	}
	&__item{
		cursor: pointer;
		@include mq() {
			flex-grow: 1;
		}
		a,span{
			display: block;
			position: relative;
			padding: 0 1em .4em;
			@include font-size(15);
			font-weight: bold;
			text-align: center;
			white-space: nowrap;
			@include p-before(100%, 4px){
				bottom: -4px;
				left: 0;
			}
			&:hover{
				@media (any-hover: hover) {
					color: #5BC0DE;
					&:before{
						background: #5BC0DE;
					}
				}
			}
			@include mq(xs){
				@include font-size(13);
			}
		}
		&.is-active{
			a, span{
				color: #5BC0DE;
				&:before{
					background: #5BC0DE;
				}
			}
			cursor: default;
		}
	}
	&--02{
		@extend .m-subnav;
		display: inline-flex;
		@include mq(md-min){
			margin: 0 auto 21px;
		}
		@include mq(){
			margin-bottom: 15px;
		}
		.m-subnav{
			&__item{
				a{
					@include font-size(14.5);
				}
			}
		}
	}
}

.c-example{
	&__button{
		display: inline-block;
		padding: 2px 0 2px 23px;
		font-weight: 500;
		@include font-size(14);
		background: url(../img/common/icn_help.png) no-repeat left center;
		background-size: 19px auto;
		cursor: pointer;
		> span{
			text-decoration: underline;
		}
		&--mgl{
			margin-left: 30px;
		}
	}
	&__cont{
		position: relative;
		margin-top: 10px;
		padding: 20px 30px;
		width: 100%;
		text-align: left;
		background: rgba(#5BC0DE,.15);
		@include mq() {
			padding: 20px 5%;
		}
	}
	&__close{
		position: absolute;
		right: 0;
		top: 0;
		width: 30px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		@include font-size(18);
		cursor: pointer;
		@include mq() {
		}
	}
	&--02{
		margin-bottom: 6px;
		@include mq(){
			margin-bottom: 28px;
		}
		.c-example{
			&__button{
				padding: 2px 40px 2px 0;
				background: url(../img/common/icn_help.png) no-repeat right center;
				background-size: 19px auto;
				span{
					@include font-size(18);
					font-weight: 700;
					text-decoration: none;
				}
			}
			&__cont{
				margin-bottom: 15px;
			}
		}
	}
	
}


