		}
		&-item{
			@include font-size(15);
			font-weight: 500;
			@include flex(between, center);
			margin-bottom: 14px;
			@include mq(){
				margin-bottom: 13px;
			}
			&:last-of-type{
				margin-bottom: 17px;
			}
		}
		&-label{
			color: #969696;
		}
		&-result{
			padding-right: 11px;
			@include mq(){
				padding-right: 4px;
			}
		}
	}
	&--02{
		margin-bottom: 22px;
		@include mq(){
			margin-bottom: 2px;
		}
		.sidebar-cumulative{
			&__calculator{
				@include mq(){
					padding: 0 20px 4px 20px;
				}
				&-item{
					@include font-size(14.5);
				}
				&-result{
					color: #5BC0DE;
				}
			}
		}
	}
