#page-P010-2,
#page-P014-2,
#page-P089-2,
#page-P090-2,
#page-P091-2,
#page-P092-2{
	.request-box{
		margin-bottom: 20px;
		@include mq(){
			margin-bottom: 0;
			.c-ttl__02{
				margin-bottom: 0;
			}
		}
	}
	.request-form__ttl {
		width: 164px;
		padding-left: 0;
		@include mq(){
			@include flex(start,center);		
			width: 100%;
			margin-bottom: 9px;
		}
	}
	.request-form__ct {
		width: calc(100% - 164px);
		@include mq(md-min){
			@include flex(start,center,wrap);		
		}
		@include mq(){
			width: 100%;
		}
	}
}

.vw100-sp{
	@include mq(){
		background: #FAFAFA;
		padding: 25px;
		margin: 0 calc(((100vw - 100%) / 2) * -1) 16px;
		text-align: center;
		+ .hr{
			display: none;
		}
	}
}
