//====================================================
//Margin PC and SP
//====================================================
@for $i from 0 through 100 {
	@include mq(md-min) {
		.mbpc-#{$i} {
			margin-bottom: $i + px
		}
	}
	@include mq() {
		.mbsp-#{$i} {
			margin-bottom: $i + px;
		}
	}
	@include mq(md-min) {
		.mtpc-#{$i} {
			margin-top: $i + px;
		}
	}
	@include mq() {
		.mtsp-#{$i} {
			margin-top: $i + px;
		}
	}
}





