@import "vars";

.quote-list{
	@include mq(){
		margin-bottom: 33px;
	}
	&__item{
		margin-bottom: 30px;
		border-bottom: 1px solid #E0E1E3;
		padding-bottom: 28px;
		@include mq(){
			padding-bottom: 26px;
		}
		&-wrapbd{
			//margin-bottom: 90px;
			//padding-bottom: 24px;
			//border-bottom: 1px solid #E0E1E3;
			//border-top: 1px solid #E0E1E3;
			padding-top: 29px;
			@include mq{
				//margin-bottom: 50px;
				//padding-bottom: 20px;
				padding-top: 20px;
			}
			&:first-child{
				border-top: none;
			}
			.request-form__ttl{
				margin-bottom: 13px;
				&.w100{
					width: 100%;
				}
			}
			&:last-child{
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: none;
			}
		}
		&:last-child{
			margin-bottom: 20px;
			padding-bottom: 0;
			border-bottom: 0;
		}
		&--223{
			@include mq(md-min){
				.quote-list__fee-item{
					.quote-list__fee-ttl{
						width: 223px;
						.required{
							margin-left: 5px;
						}
					}
					.quote-list__fee-ct{
						width: calc(100% - 223px);
					}
				}
			}
			
		}
	}
	&--02{
		.quote-list__item{
			&:last-child{
				margin-bottom: 32px;
				padding-bottom: 30px;
				border-bottom: 1px solid #E0E1E3;
			}
		}
	}
	&__ttl{
		color: #464646;
		font-weight: bold;
		line-height: 1.3;
		margin-bottom: 13px;
		@include font-size(16);
	}
	.c-txt__01{
		margin-bottom: 15px;
	}
	&__date{
		@include flex(0,center,wrap);
		margin-bottom: 6px;
		@include mq(){
			margin-bottom: 20px;
			@include flex(0,0,wrap);
			&:last-child{
				margin-bottom: 0;
			}
		}
		&--start{
			align-items: flex-start;
			.quote-list__date-ttl{
				padding-top: 15px;
			}
		}
		&:last-child{
			margin-bottom: 0;
		}
		&-ttl{
			color: #464646;
			font-weight: bold;
			@include font-size(14);
			width: 65px;
			padding-left: 12px;
			padding-right: 10px;
			small{
				color: #F04850;
				font-weight: 500;
				margin-left: 10px;
			}
			@include mq(){
				width: 100%;
				padding: 0;
				margin-bottom: 7px;
			}
		}
		&-list{
			@include flex(0,0,wrap);
			width: calc(100% - 65px);
			@include mq(){
				width: 100%;
			}
			&--nocalendar{
				@include mq(){
					flex-wrap: nowrap;
				}
				.quote-list__date-item{
					&:first-child{
						@include mq(md-min){
							max-width: 84px;
							@include mq(xl){
								max-width: 72px;
							}
						}
						
					}
					&:last-child{
						@include mq{
							margin-right: 0;
						}
					}
					@include mq{
						width: 100%;
						max-width: 171px;
					}
					.form_part_select{
						@include mq{
							width: 100%;
						}
					}
				}
			}
			&--02{
				width: 100%;
				@include flex(0, center);
				margin-bottom: 14px;
				.quote-list__date{
					&-item{
						@include mq{
							margin-bottom: 10px;
						}
						&.w107{
							max-width: 107px;
							.form_part_select{
								width: 107px;
							}
						}
					}
					&-list{
						&__wrap{
							display: flex;
						}
					}
					&-between{
						margin: 0 18px 0 5px;
					}
				}
			}
		}
		&-item{
			margin-right: 10px;
			@include mq(xs){
				margin-right: 5px;
			}
			&:first-child{
				max-width: 208px;
				width: 100%;
				@include mq(xl){
					max-width: 136px;
				}
				@include mq(){
					width: 100%;
					max-width: 171px;
				}
				@include mq(xs){
					max-width: 125px;
				}
			}
			&:nth-child(3){
				margin-right: 0;
			}
			.form_part_select{
				width: 84px;
				@include mq(xl){
					width: 72px;
					select{
						padding: 5px 6px;
					}
				}
			}
			.form-edit__input{
				max-width: 207px;
				width: 100%;
				@include mq(){
					max-width: 171px;
				}
			}
		}
		&-note{
			width: 100%;
			margin-top: 18px;
		}
		&--02{
			@include mq(md-min){
				margin-bottom: 13px;
				.quote-list__date-ttl{
					width: 158px;
					@include mq(xl){
						width: 100px;
					}
					span{
						color: #5BC0DE;
						font-weight: bold;
						@include font-size(13);
					}
				}
				.quote-list__date-list{
					width: calc(100% - 158px);
					@include mq(xl){
						width: calc(100% - 100px);
					}
				}
			}
		}
		&-day{
			color: #5BC0DE;
			font-weight: 500;
			@include font-size(13);
		}
		
	}
	&__box{
		margin-bottom: 21px;
		padding-right: 12px;
		@include mq(xl){
			padding-right: 0;
		}
		&-ttl{
			color: #464646;
			font-weight: bold;
			@include font-size(16);
			background: rgba(#5BC0DE,0.15);
			padding: 17px 19px;
			border: 1px solid rgba(#707070,0.15);
			border-bottom: 0;
			@include mq(){
				padding: 15px;
			}
		}
		&-ct{
			padding: 20px;
			border: 1px solid rgba(#707070,0.15);
			@include mq(){
				padding: 15px;
			}
		}
		&-desc{
			color: #464646;
			font-weight: bold;
			@include font-size(16);
			margin-bottom: 20px;
		}
		&-price{
			@include flex(0,center,wrap);
			gap: .5em 1em;
			color: #464646;
			font-weight: bold;
			@include font-size(20);
			@include mq(){
				gap: .5em;
			}
			img{
				margin-right: .4em;
			}
			span{
				@include font-size(14);
				span{
					color: #969696;
				}
			}
			&-item{
				@include flex(0,center);
				b{
					margin-right: 12px;
					display: inline-block;
					font-weight: bold;
					color: #5BC0DE;
					padding-left: 10px;
					@include font-size(14.5);
					@include mq(){
						padding-left: 0;
					}
				}
			}
		}
	}
	&__option{
		@include flex(0,center);
		border-bottom: 1px solid #E0E1E3;
		padding-bottom: 19px;
		margin-bottom: 16px;
		@include mq(){
			padding-bottom: 17px;
			margin-bottom: 15px;
			@include flex(0,0,wrap);
		}
		&:last-child{
			margin-bottom: 17px;
			padding-bottom: 0;
			border-bottom: 0;
			margin-top: 30px;
		}
		&-ttl{
			color: #464646;
			font-weight: 500;
			width: 202px;
			padding-right: 15px;
			@include font-size(14);
			@include mq(){
				width: 100%;
				padding-right: 0;
				margin-bottom: 17px;
			}
		}
		&-ct{
			width: calc(100% - 202px);
			@include mq(){
				width: 100%;
				.quote-list__fee-select{
					margin-bottom: 0;
				}
			}
		}
	}
	&__fee{
		&-item{
			@include flex(0,center);
			margin-bottom: 15px;
			@include mq(){
				@include flex(0,0,wrap);
			}
			&:last-child{
				margin-bottom: 0;
			}
			&--start{
				align-items: flex-start;
			}
		}
		&-flex{
			@include flex();
			margin-top: 10px;
			.quote-list__fee-input{
				max-width: 196px;
				width: 100%;
				margin-right: 20px;
				@include mq(){
					margin-right: 15px;
				}
			}
			.form_part_select{
				max-width: 136px;
				width: 100%;
			}
		}
		&-ttl{
			width: 158px;
			padding-right: 10px;
			padding-left: 14px;
			color: #464646;
			font-weight: bold;
			@include font-size(14);
			line-height: 1.875;
			@include mq(){
				width: 100%;
				padding: 0;
				margin-bottom: 10px;
			}
			span{
				color: #F04850;
				font-weight: 500;
				margin-left: 10px;
			}
			&.top{
				@include mq(md-min){
					align-self: flex-start;
					padding-top: .9em;
				}
			}
		}
		&-ct{
			padding-right: 12px;
			width: calc(100% - 158px);
			@include mq(xl){
				padding-right: 0;
			}
			@include mq(){
				width: 100%;
				margin-right: 0;
			}
			.textarea{
				height: 224px;
				margin-bottom: 14px;
				@include mq(){
					margin-bottom: 19px;
				}
				&.h178{
					height: 178px;
				}
				&.h407{
					height: 407px;
					margin-bottom: 7px;
				}
				&.h417{
					height: 417px;
					margin-bottom: 8px;
				}
				&.h112{
					height: 112px;
				}
				&.h237{
					height: 237px;
					margin-bottom: 7px;
				}
				&.h86{
					height: 86px;
					@include mq(){
						height: 103px;
					}
				}
				&.h170{
					height: 170px;
					@include mq(){
						height: 364px;
					}
				}
				&.h153{
					height: 153px;
					@include mq(){
						height: 364px;
					}
				}
				&.h230{
					height: 230px;
					margin-bottom: 0;
				}
			}
			
		}
		&-wf{
			@include flex(0, 0);
			@include mq{
				flex-wrap: wrap;
			}
			&option{
				width: 230px;
				max-width: calc(100% - 23px);
				margin-right: 23px;
				@include flex(0,center);
				&:last-child, &:nth-child(2n){
					margin-right: 0;
				}
				@include mq{
					width: 100%;
					margin-right: 0;
					margin-bottom: 10px;
					&:last-child{
						margin-bottom: 0;
					}
				}
			}
			.quote-list__option-ttl{
				width: auto;
				margin-right: 9px;
				padding-right: 0;
				min-width: 42px;
			}
			&ct{
				width: calc(100% - 42px);
			}
		}
		&-input{
			border: 1px solid #E0E1E3;
			border-radius: 4px;
			padding: 3px;
			display: inline-flex;
			align-items: center;
			background: #fff;
			justify-content: space-between;
			@include mq(){
				display: flex;
			}
			span{
				background: #F2F0EE;
				border-radius: 4px;
				padding: 10px 5px 10px 5px;
				@include flex(center,center);
				height: 43px;
				min-width: 33px;
				color: #464646;
				font-weight: bold;
				@include font-size(13);
			}
			.form-edit__input{
				border: 0;
			    width: 105px;
				padding: 15px 6px;
				height: 43px;
			}
			
		}
		&-note{
			color: #969696;
			font-weight: 500;
			line-height: 1.538;
			margin-bottom: 16px;
			@include font-size(13);
			span{
				display: block;
				color: #464646;
				font-weight: 500;
				margin-bottom: 3px;
				@include font-size(14);
			}
			a{
				color: #5BC0DE;
				display: inline-block;
				text-decoration: underline;
			}
			&--02{
				line-height: 2;
				padding-top: 5px;
			}
		}
		&-result{
			background: #FAFAFA;
			border-radius: 4px;
			padding: 19px 21px;
			color: #464646;
			@include font-size(14);
			@include mq(xl){
				padding: 19px 14px;
			}
			span{
				color: #969696;
				@include font-size(12);
			}
		}
		&-start{
			margin-bottom: 10px;
		}
		&-row{
			@include flex(0,center,wrap);
			@include mq(){
				justify-content: flex-end;
			}
			.c-example__cont{
				width: 100%;
				margin-top: 10px;
			}
			.statuschips{
				margin-left: 30px;
			}
			.quote-list__fee-input{
				@include mq{
					width: 100%;
					margin-bottom: 15px;
					&:last-child{
						margin-bottom: 0;
					}
				}
			}
		}
		&-add{
			margin-right: 11px;
			margin-top: 19px;
			@include mq(xl){
				margin-right: 0;
			}
			.quote-list__fee-note{
				margin-bottom: 0;
			}
			.form_part_select--02{
				margin-left: 20px;
				width: 136px;
			}
		}
		&-form{
			padding: 15px 18px;
			margin-bottom: 20px;
			background: rgba(#5BC0DE,0.15);
			@include flex(0,center,wrap);
			@include mq(xl){
				padding: 15px 10px;
			}
			@include mq(){
				margin-bottom: 24px;
				padding: 15px 17px;
			}
			&.on{
				pointer-events: none;
				opacity: 0;
				height: 0;
				margin: 0;
				padding: 0;
			}
			.c-btn__07{
				margin-left: 25px;
				@include mq(xl){
					margin-left: 10px;
				}
				@include mq(){
					margin-left: 12px;
				}
			}

			&--02{
				@include mq(){
					.quote-list__fee-input{
						width: calc(50% - 9px);
						margin-right: 9px;
						margin-bottom: 15px;
						.form-edit__input{
							max-width: 105px;
							width: 100%;
						}
					}
					.form_part_select{
						width: calc(50% - 9px);
						margin-left: 9px;
						margin-bottom: 15px;
					}
					.c-btn__07{
						margin-left: auto;
					}
				}
			}
		}
		&-inputxt{
			width: 100%;
			margin-bottom: 15px;
		}
		&-keep{
			width: 235px;
			@include flex(0,center,center);
			@include mq(){
				width: 100%;
				margin-bottom: 15px;
			}
			p{
				color: #464646;
				font-weight: bold;
				margin-left: 10px;
				@include font-size(14.5);
			}
		}
		&-remove{
			margin-bottom: 29px;
			// padding: 0 10px 0 19px;
		    justify-content: space-between;
			&.on{
				@include flex(between,center,wrap);
			}
			@include mq(){
				padding: 0;
				margin-bottom: 13px;
			}
			p{
				color: #464646;
				font-weight: 500;
				@include font-size(14);
				@include mq(){
					width: 100%;
					margin-bottom: 15px;
				}
			}
			.btn-list{
				@include mq(){
					width: 100%;
					justify-content: flex-end
				}
			}
		}
		&-select{
			@include flex(0,center);
			margin-right: 20px;
			@include mq(){
				width: 100%;
				margin-bottom: 20px;
			}
			span{
				width: 40px;
				font-weight: 500;
				text-align: center;
				color: #969696;
				@include font-size(12);
			}
			.form_part_select{
				width: 80px;
			}
		}
	}
	
	&__note{
		@include font-size(13);
		font-weight: 500;
		color: #969696;
		margin-top: -6px;
		width: calc(100% - 158px);
		margin-left: auto;
		margin-right: 0;
	}
	
	&__schedule{
		@include font-size(13);
		font-weight: 500;
		color: #5BC0DE;
		@include flex();
		margin-bottom: 13px;
		@include mq(){
			display: none;
		}
		&-l{
			width: 158px;
			font-weight: 700;
			padding-left: 12px;
			padding-right: 10px;
		}
		&-r{
			width: calc(100% - 158px);
		}
	}

	&--03{
		@include mq(xl-min){
			.quote-list__item{
				margin-bottom: 19px;
    			padding-bottom: 20px;
				&:last-child{
				    margin-bottom: 44px;
				}
			}
			.quote-list__fee-ttl{
				width: 178px;
				padding: 0;
			}
			.quote-list__fee-ct{
				padding-right: 0;
				width: calc(100% - 178px);
				.form-add{
					margin-top: 6px;
				}
				
			}
		}
		@include mq(){
			.quote-list__item{
				padding-bottom: 20px;
				margin-bottom: 13px;
				&:last-child{
					margin-bottom: 49px;
				}
			}
			.quote-list__fee-ttl{
				margin-bottom: 14px;
			}
		}
	}
}

#page-P011-3{
	.use-sidebar-main{
		margin-bottom: 12px;
	}
}