@use "vars" as *;
.txt-underline{
	text-decoration: underline;
}
#page-P024{
	@include mq{
		padding-bottom: 0;
	}
}
.myprofile-box{
	padding: 43px 74px 63px;
	background: #fff;
	@include mq(tb){
		padding: 43px 25px 63px;
	}
	@include mq{
		padding: 15px 0 0;
	}
	.l-content__02{
		padding: 0;
		@include mq{
			padding: 0 15px;
		}
	}
}
.sec-myprofile{
	@include mq{
		margin-top: -12px;
	}
	.user-status__num{
		color: #707070;
	}
	.sidebar-tags{
		justify-content: flex-start;
		margin-bottom: 0;
		@include mq{
			padding-top: 0;
		}
	}
	.sidebar-ages__w{
		@include flex(0, 0, wrap);
		.sidebar-ages__heading{
			padding: 0 15px 0 0;
			position: relative;
			margin-right: 15px;
			font-weight: 500;
			@include mq(sm){
				margin-right: 0;
				padding-right: 0;
				padding-left: 11px;
				margin-bottom: 14px;
				min-width: 55%;
				justify-content: flex-start;
				&:last-child{
					margin-bottom: 0;
				}
			}
			&:last-child{
				padding-right: 0;
				margin-right: 0;
			}
			&:after{
				content: '';
				width: 1px;
				height: 18px;
				background: #E0E1E3;
				position: absolute;
				right: 0;
				@include v-align;
				@include mq(sm){
					content: none;
				}
			}
			&:last-child{
				&:after{
					content: none;
				}
			}
			span{
				&:first-child{
					margin-right: 15px;
				}
			}
			.c-green{
				color: #5BC0DE;
			}
		}
	}
	.l-content__02{
		margin-bottom: 25px;
		@include mq(md-min){
			grid-template-columns: 1fr 375px;
			gap: 30px 28px;
			@include mq(tb){
				grid-template-columns: 1fr 270px;
			}
		}
		@include mq{
			margin-bottom: 37px;
		}
		&-r{
			margin-top: 10px;
			@include mq{
				margin-top: 0;
			}
		}
	}
}

.myprofile-heart{
	position: absolute;
	right: 0;
	top: 55px;
	@include mq{
		top: 46px;
	}
	&sub{
		margin-bottom: 11px;
		@include flex(end, center);
	}
	&__txt{
		@include fts(12, 16);
		font-weight: 400;
		color: #707070;
		margin-right: 8px;
	}
	.icon{
		display: inline-block;
	}
}
.myprofile-ct{
	position: relative;
	padding-left: 14px;
	@include mq{
		padding-left: 0;
	}
	.use-box__address{
		padding-left: 8px;
		& > span{
			white-space: nowrap;
		}
	}
	.user-heading {
		padding-left: 10px;
		margin-bottom: 12px;
		.user-status__medal{
			margin-right: 5px;
			& > span {
				width: 20px;
				height: 27px;
			}
		}
		.user-status__star{
			& > span{
				width: 103px;
				height: 18px;
				&:after{
					background-size: auto 17.5px;
				}
			}
		}
	}
	.sidebar-tags__item{
		margin:0 8px 11px;
		@include mq{
			margin: 0 5px 11px;
		}
	}
	.sidebar-tags__item--bd{
		margin: 0 6px 11px;
		@include fts(14 , 14);
		@include mq{
			margin: 0 6px 15px;
		}
	}
	
}

.myprofile-user{
	margin-top: -46px;
}
.quote-list__box{
	&-link{
		a{
			@include fts(12, 24);
			font-weight: 500;
			padding: 7px 15px 0 11px;
			position: relative;
			width: 100%;
			text-align: center;
			margin-top: 24px;
			border-top: 1px solid #E0E1E3;
			&:after{
				content: '';
				width: 6px;
				height: 6px;
				border-top: 1px solid #5BC0DE;
				border-right: 1px solid #5BC0DE;
				display: inline-block;
				transform: rotate(135deg);
				margin-left: 8px;
				position: relative;
				top: -3px;
			}
		}
	}
	&-orange{
		border: 1px solid #E57200;
		padding: 12px 18px 15px;
		border-radius: 4px;
		margin-bottom: 27px;
		@include mq{
			padding: 12px 18px 5px;
		}
		.c-txt__01{
			line-height: 1.75;
		}
	}
}

.myprofile-info{
	.quote-list__box{
		&-ct{
			margin-bottom: 20px;
			padding: 31px 18px 8px;
			@include mq{
				padding: 30px 0 9px;
				border: none;
				position: relative;
				&:after{
					content: '';
					width: 100%;
					height: 1px;
					background: #E0E1E3;
					position: absolute;
					bottom: 0;
					width: calc(100% + 30px);
					margin: 0 -15px;
				}
			}
		}
		&-price{
			@include flex(0, 0);
			margin-left: 5px;
			gap: 0.5em 1.5em;
			@include mq{
				margin-left: 0;
				gap: 0.5em 0.5em;
			}
		}
		
	}
	&__ct{
		&-interview{
			padding: 19px 20px;
			border: 1px solid #E0E1E3;
			border-radius: 4px;
			margin-bottom: 12px;
			@include mq{
				padding: 19px 20px 11px;
				margin-bottom: 22px;
			}
			.quote-list__box-desc{
				font-size: 1.4rem;
				margin-bottom: 15px;
			}
			.quote-list__box-link{
				a{
					margin-top: 13px;
				}
			}
			.r-flsp03{
				@include mq{
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					display: block;
					display: -webkit-box;
				}
			}
		}
		&-w{
			@include flex(0, start);
			margin-bottom: 14px;
			& > *{
				width: calc(50% - 5px);
				margin-right: 10px;
				text-align: left;
				&:last-child, &:nth-child(2n){
					margin-right: 0;
				}
			}
			.quote-list__box-price{
				margin-left: 0;
			}
			
		}
		&-ttl{
			@include fts(13, 24);
			font-weight: 300;
			color: #5BC0DE;
			margin-bottom: 5px;
			@include mq{
				margin-bottom: 2px;
			}
		}
		
	}
}
.myprofile__pleasant{
	padding: 40px 20px;
	border: 1px solid #E0E1E3;
	border-radius: 4px;
	margin-bottom: 41px;
	@include mq{
		padding: 0;
		border: none;
		margin-bottom: 17px;
	}
	&-block{
		max-width: 780px;
		margin: 0 auto;
		.c-ttl__02{
			line-height: 1.388;
		}
	}
	&-blue{
		padding: 9px 11px;
		background: #E6F6FA;
		border-radius: 4px;
		margin-bottom: 25px;
		font-weight: 600;
		span{
			margin-right: 2px;
		}
	}
	&-dl{
		@include flex(0,0);
		margin-bottom: 7px;
		@include mq(){
			flex-wrap: wrap;
		}
		&--nowrap{
			@include mq(){
				flex-wrap: nowrap;
				.myprofile__pleasant-dt{
					width: 70px;
				}
				.myprofile__pleasant-dd{
					width: calc(100% - 70px);
				}
			}
		}
	}
	&-dt{
		min-width: 70px;
		font-weight: 700;
		padding-right: 30px;
		white-space: nowrap;
		@include mq(){
			width: 100%;
		}
	}
	&-dd{
		width: calc(100% - 70px);
		@include mq(){
			width: 100%;
		}
	}
	&-txt{
		line-height: 1.7142;
		p{
			margin-bottom: 25px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	&-start{
		@include mq{
			display: flex;
			margin-bottom: -3px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}
.block-service{
	&__dots{
		line-height: 1.75;
		.block-service__item{
			&:before{
				content: '・';
				
			}
		}
	}
	&__list{
		line-height: 1.7;
		.block-service__item{
			&:before{
				content: "●";
				font-size: 2.8rem;
				line-height: 0.7;
				position: relative;
				left: -3pxc-txt__01 mbpc-17;
				top: 0px;
			}
		}
	}
	&__item{
		@include flex(0,0);
		
	}
	.m-tabs02__content-item{
		padding: 0 0px 0 20px;
		@include mq{
			padding: 0 0 0 5px;
		}
		.quote-list__box-price{
			gap:0.5em 2em;
			@include mq(xs){
				gap: 0.5em 0.5em;
			}
		}
	}
}
.block-calendartime{
	position: relative;
	padding-left: 20px;
	margin-bottom: 49px;
	@include mq{
		padding-left: 0;
		margin-bottom: 28px;
	}
	.c-example__buttont{
		position: absolute;
		right: 0;
		top: 3px;
		@include fts(14, 25);
		font-weight: 500;
		span{
			text-decoration: underline;
		}
	}
	.m-tabs{
		justify-content: flex-start;
		margin-bottom: 9px;
	}
	.m-tabs__content-itemt{
		padding: 15px 15px;
		background: #F2F0EE;
		text-align: center;
		margin-bottom: 10px;
		.month-slider{
			margin: 0 auto;
		}
	}
	.achievement-block__r{
		@include mq{
			padding-bottom: 30px;
			flex-wrap: nowrap;
		}
	}
	.achievement-block{
		@include mq{
			margin-bottom: 14px;
		}
	}
	.m-schedule__table{
		.td-root{
			min-height: 120px;
			width: 100%;
			@include flex(center, center);
			@include mq{
				min-height: 63px;
			}
		}
	}
	.calendar-slider{
		.m-schedule__table{
			@include mq{
				margin: 0 0 20px;
			}
		}
	}
	.m-schedule__table{
		table{
			tr{
				th{
					@include font-size(16);
				}
			}
		}
	}
}
.block-checkmain{
	padding-left: 20px;
	@include mq{
		padding-left: 0;
	}
	.job-infor__list--modify{
		@include mq{
			padding: 0 4px;
		}
	}
	&__btn{
		@include mq{
			width: calc(100% + 40px);
			margin: 0 -20px;
			padding: 20px 20px;
			background: #F7F7F7;
		}
	}
}
.table-time{
	max-width: 345px;
	margin: 0 auto 50px;
	@include mq{
		margin-bottom: 25px;
	}
}
.m-schedule__table-timesquare{
	@include fts(10, 13);
	font-weight: 500;
	color: #464646;
	width: 38px;
	height: 38px;
	border-radius: 4px;
	border: 2px solid #F6D5D0;
	@include flex(center, center);
	background: #ffffff;
	@include mq{
		width: 35px;
		height: 35px;
	}
}

.js-showadd{
	text-align: center;
	span{
		@include fts(12, 18);
		font-weight: 500;
		color: #5BC0DE;
		position: relative;
		display: inline-block;
		margin: 10px auto 25px;
		transition: height 0.3s ease-in;
		&:after{
			content: "";
			width: 6px;
			height: 6px;
			border-top: 1px solid #5BC0DE;
			border-right: 1px solid #5BC0DE;
			display: inline-block;
			-webkit-transform: rotate(135deg);
			-ms-transform: rotate(135deg);
			transform: rotate(135deg);
			margin-left: 8px;
			position: relative;
			top: -3px;
		}
	}
}
.js-height{
	transition: height 0.3s ease-in;
}
.js-wishlist{
	&.wishlist{
		.img-normal{
			display: none;
		}
		.img-heart{
			display: block;
		}
	}
}

.js-wishlist__icon{
	padding-left: 0;
	.img-normal{
		display: block;
	}
	.img-heart{
		display: none;
	}
	.checkmark{
		display: none;
	}
}