@use "vars" as *;
.sec-question{
	.l-content__03{
		@include mq(md-min){
			grid-template-columns: 250px 1fr;
			gap: 30px 39px;
			padding: 30px 20px 70px 24px;
			&-r{
				padding-top: 0;
			}
		}
		@include mq{
			padding-top: 0;
			gap: 20px 20px;
		}
		&-l{
			@include mq{
				margin: 0 calc(((100vw - 100%) / 2) * -1) 0;
			}
		}
	}
}
.question-t{
	padding: 20px 72px 20px 178px;
	background: #5BC0DE;
	border-radius: 4px;
	margin-bottom: 23px;
	position: relative;
	@include mq{
		padding: 20px 16px;
		margin: 0 calc(((100vw - 100%) / 2) * -1) 0;
		border-radius: 0;
	}
	&__l{
		position: absolute;
		bottom: -2px;
		left: 42px;
		@include mq{
			display: none;
		}
	}
	
}
.input-search{
	@include fts(14, 21);
	color: #464646;
	background: #ffffff;
	box-shadow: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	cursor: pointer;
	width: 100%;
	padding: 9.5px 45px 9.5px 17px;
	@include mq{
		padding: 9.5px 45px 9.5px 4px;
	}
	&::-ms-expand {
		display: none;
	}
	
	&::placeholder {
		color: #969696;
	}
	@include mq{
		@include fts(12, 18);
	}
}
.search-block{
	border: 1px solid #E0E1E3;
	background: #ffffff;
	position: relative;
	button{
		position: absolute;
		top: 0;
		right: 0;
		width: 40px;
		height: 40px;
		z-index: 2;
	}
}
.question-asidettl{
	@include fts(14, 21);
	font-weight: 700;
	padding: 13px 10px 13px 49px;
	background: #E8F7FB;
	@include mq{
		padding: 13px 10px 13px 44px;
	}
}
.question-asidelist{

}
.question-asideitem{
	@include fts(14, 21);
	font-weight: 400;
	
	border-bottom: 1px solid #E8E8E8;
	@include mq{
		padding: 0 13px;
	}
	&:first-child{
		border-top: 1px solid #E8E8E8;
	}
	a{
		position: relative;
		padding: 14px 31px 14px 63px;
		display: block;
		@include mq{
			padding: 14.2px 43px 14.2px 59px;
		}
		&:before{
			content: '';
			width: 11px;
			height: 11px;
			background: url('../img/common/img-arrow.svg') no-repeat center center;
			background-size: 100%;
			display: inline-block;
			position: absolute;
			top: 50%;
			left: 31px;
			transform: translateY(-50%);
		}
		&:hover{
			opacity: 0.75;
		}
	}
}
.question-wrap{
	@include flex(0, 0, wrap);
}
.question-block{
	width: calc(50% - 9.25px);
	margin-right: 18.5px;
	border: 1px solid #E0E1E3;
	padding: 19px 26px 18px;
	border-radius: 4px;
	margin-bottom: 20px;
	&:last-child, &:nth-child(2n){
		margin-right: 0;
	}
	@include mq{
		width: 100%;
		margin-right: 0;
	}
	&__ttl{
		@include fts(18, 27);
		font-weight: 700;
		color: #5BC0DE;
		margin-bottom: 33px;
		text-align: center;
		@include  flex(center, center);
		img{
			margin-right: 11px;
		}
	}
	&__list{
		margin-bottom: 19px;
		&:last-child{
			margin-bottom: 0;
			.question-block__item{
				&:last-child{
					border-bottom: none;
				}
			}
		}
		
	}
	&__item{
		border-bottom: 1px solid #E0E1E3;
		&:first-child{
			border-top: 1px solid #E0E1E3;
		}
		a{
			@include fts(14.5, 21.75);
			font-weight: 500;
			display: inline-block;
			width: 100%;
			position: relative;
			padding: 15px 0;
			&:hover{
				opacity: 0.75;
			}
			&:after{
				content: "";
				border: solid #464646;
				border-width: 0 2px 2px 0;
				display: inline-block;
				padding: 2.5px;
				-webkit-transform: rotate(-45deg) translateY(-50%);
				-ms-transform: rotate(-45deg) translateY(-50%);
				transform: rotate(-45deg) translateY(-50%);
				position: absolute;
				top: 47%;
				right: 0px;
			}
		}
	}
}
.question-fullblock{
	.question-block__list:last-child .question-block__item:last-child{
		border-bottom: 1px solid #E0E1E3;
	}
	@include mq{
		margin: 0 calc((100vw - 100%) / 2 * -1) 0;
		.c-ttl__02{
			padding: 0 15px 0 47px;
			position: relative;
			letter-spacing: 0.12em;
			margin-bottom: 16px;
			&:before{
				content: "";
				border: solid #464646;
				border-width: 0 3px 3px 0;
				display: inline-block;
				padding: 4px;
				transform: rotate(135deg);
				position: absolute;
				top: 4px;
				left: 17px;
			}
		}
		.question-block__item{
			padding: 0 15px;
			a{
				padding: 14px 0 14px 30px;
				&:after{
					right: 7px;
				}
			}
		}
	}
}

.question-detail{
	padding-top: 9px;
	h1{
		@include fts(18, 27);
		font-weight: 700;
		margin-bottom: 18px;
		padding-bottom: 27px;
		border-bottom: 1px solid #E0E1E3;
		& + p{
			margin-bottom: 15px;
			@include mq{
				letter-spacing: 0.02em;
				line-height: 1.72;
			}
		}
		@include mq{
			position: relative;
			margin: 0 calc((100vw - 100%) / 2 * -1) 11px;
			padding: 0 15px 12px 46px;
			&:before{
				content: "";
				border: solid #464646;
				border-width: 0 3px 3px 0;
				display: inline-block;
				padding: 5px;
				transform: rotate(135deg);
				position: absolute;
				top: 6px;
				left: 19px;
			}
		}
	}
	h2{
		padding: 8px 12px;
		background: #F2F0EE;
		@include fts(16, 24);
		margin-bottom: 14px;
		font-weight: 600;
		@include mq{
			padding: 6px 5px;
			font-feature-settings: "palt";
			margin-bottom: 21px;
		}
		& + ul {
			margin-bottom: 15px;
		}
	}
	ul{
		line-height: 1.72;
		margin-bottom: -4px;
		li{
			&:before{
				content: '・';
			}
		}
		& + p{
			margin-bottom: 23px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
	img{
		padding-left: 12px;
		margin-bottom: 40px;
		@include mq{
			margin-bottom: 20px;
		}
	}
	h3{
		@include fts(14, 24);
		font-weight: 600;
		color: #5BC0DE;
		&:before{
			content: '◆';
			color: #5BC0DE;
		}
	}
}