@use "vars" as *;

/* ====================================================
HTML
==================================================== */
html {
    font-size: 62.5%;
	//font-size: 100%;//16px
	//@include mq(md-min){
	//   font-size: 62.5%;
	//}
}

body {
	color: #464646;
	font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
	font-weight:400;
	font-size: 100%;
	-webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1;
	background: #FAFAFA;
}

a {
	display: inline-block;
	color: inherit;
    text-decoration: none;
    outline: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

strong{
	font-weight: bold;
}

input, button, textarea, select {
	margin: 0;
	padding: 0;
	background: none;
	border: none;
	border-radius: 0;
	outline: none;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	&:active,
	&:focus{
		outline: 0;
	}
}

button{
	cursor: pointer;
}

.hr{
	height: 0;
	margin: 0 0 30px;
	padding: 0;
	border: 0;
	border-top: 1px solid #E0E1E3;
}