@use "vars" as *;

/* ====================================================
Header
==================================================== */
.main-header{
	width: 100%;
	height: 80px;
	background: #fff;
	border-bottom: 1px solid #E0E1E3;
	transition: .3s;
	z-index: 10;
	@include mq(md-min) {
		position: fixed;
		top: 0;
		left: 0;
	}
	@include mq() {
		height: 55px;
	}
	&__inner{
		position: relative;
		@include flex(between, center);
		margin: 0 auto;
		max-width: calc(100% + 40px);
		padding: 14px 20px;
		@include mq() {
			padding: 8px 15px;
		}
		&--center{
			justify-content: center;
			@include mq{
				padding: 16px 15px;
			}
		}
	}
	&__logo{
		width: 230px;
		@include mq() {
			width: 130px;
		}
	}
	&__navGlobal{
		@include flex(between, center ,wrap);
		width: calc(100% - 230px);
		@include mq(tb) {
			justify-content: flex-end;
			width: calc(100% - 130px);
		}
	}
	&__menu{
		@include flex(center, center);
		width: calc(100% - 306px);
		@include mq(tb) {
			display: none;
		}
		&-item{
			margin-left: 1em;
			@include font-size(15);
			@include mq(xl){
				margin-left: 1.25%;
			}
			a{
				display: block;
				padding: .8em 1em;
				color: #969696;
				border-radius: 4px;
				@include mq(xl){
					padding: .8em .5em;
				}
			}
			&.current a{
				color: #3B4043;
				font-weight: bold;
				background: #F2F0EE;
			}
		}
	}
	
	&__control{
		@include flex(start, center);
		width: 306px;
		gap: 0 5px;
		@include mq() {
			justify-content: flex-start;
			width: 156px;
			gap: 0 3px;
		}
		a{
			@include flex(center, center,wrap);
			width: 46px;
			height: 46px;
			color: #fff;
			font-weight: bold;
			text-align: center;
			background: #F04850;
			border-radius: 4px;
			@include mq() {
				width: 36px;
				height: 36px;
			}
			i{
				@include font-size(20);
				line-height: 1;
				@include mq() {
					@include font-size(17);
				}
			}
			&.disabled{
				pointer-events: none;
				background: #E0E1E3;
			}
		}
		.btn-release{
			width: 102px;
			color: #fff;
			@include font-size(16);
			background: #5BC0DE;
			@include mq() {
				display: none;
			}
			i{
				margin-right: .2em;
				@include font-size(15);
			}
		}
		.btn-check,
		.btn-mail,
		.btn-info{
			padding: 4px 0;
			> span{
				display: block;
				width: 100%;
				@include font-size(13);
			}
		}
	}
	&__mobile-icon {
		display: block;
		position: relative;
		width: 46px;
		height: 46px;
		color: #fff;
		background: #464646;
		transition: background .5s;
		border-radius: 4px;
		cursor: pointer;
		z-index: 100;
		@include mq() {
			width: 36px;
			height: 36px;
		}
		&-border{
			position: absolute;
			top: 50%;
			left: 13px;
			display: block;
			width: 20px;
			height: 2px;
			user-select: none;
			transition: background-color 0.3s;
			background: #fff;
			@include mq() {
				left: 9px;
			}
			&:nth-child(1){
				margin-top: 5px;
				width: 12px;
			}
			&:nth-child(3){
				margin-top: -5px;
			}
		}
		&.mobile-close{
			position: fixed;
			right: 20px;
			top: 16px;
			background: none;
			@include mq() {
				right: 15px;
				top: 8px;
			}
			.main-header__mobile-icon-border{
				background: #969696;
			}
		}

	}	
	

	//active
	.user-menu{
		position: fixed;
		top: 0;
		//top: 80px;
		right: -100%;
		width: 90%;
		max-width: 330px;
		height: 100vh;
		overflow-y: scroll;
		background: #fff;
		transition: .5s;
		opacity: 0;
		z-index: 99;
		&.is-show{
			opacity: 1;
			right: 0;
			background: #fff;
			box-shadow: 0 0 10px rgba(#000, .1);
			.main-header__name{
				color: #000;
			}
			.main-header__menu-item{
				color: #000;
			}
		}
		&__heading{
			@include flex(start, center);
			padding: 10px 25px 10px 15px;
			background: #F2F0EE;
		}
		&__heading-image{
			width: 60px;
			height: 60px;
			overflow: hidden;
			border-radius: 50%;
		}
		&__heading-body{
			padding-left: 10px;
			width: calc(100% - 90px);
		}
		&__status{
			@include flex(start, center);
		}
		&__name{
			margin-bottom: 5px;
			@include font-size(20);
			font-weight: bold;
		}
		&__star{
			> span{
				width: 90px;
				height: 15px;
				&:after{
					background-size: auto 15px;
				}
			}
		}
		&__nav{
			padding: 5px 0 50px 35px;
			@include font-size(15);
		}
		&__nav-item{
			border-bottom: 1px solid #E0E1E3E3;
			> a,
			> span,
			> button{
				display: block;
				padding: 1.1em 0;
				border: 0;
				background: none;
				outline: 0;
			}
			.btn-acc{
				position: relative;
				@include p-after(25px,25px){
					right: 20px;
					@include v-align;
					color: #F2F0EE;
					text-align: center;
					@include font-size(26);
					@include ft-awesome(f139);	
				}
				@include p-before(24px,24px){
					right: 20px;
					@include v-align;
					background: #969696;
					border-radius: 50%;
				}
				&.btn-acc-close{
					&:after{
						@include ft-awesome(f13a);	
					}
				}
			}
			.acc-cont{
				padding-bottom: .5em;
			}
		}
		&__nav-item-child{
			a{
				display: block;
				padding: 1em 0 1em 1em;
			}
		}
	}
}




// mobile menu btn animation
	.main-header__mobile-icon-border:nth-child(1) {
		-webkit-animation: click-header-sp-bar-1-close 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards;
		animation: click-header-sp-bar-1-close 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards
	}
	
	@-webkit-keyframes click-header-sp-bar-1-close {
		0% {
			transform: translateY(5px) rotate(45deg)
		}
		50% {
			transform: translateY(5px) rotate(0)
		}
		100% {
			transform: translateY(0) rotate(0)
		}
	}
	@keyframes click-header-sp-bar-1-close {
		0% {
			transform: translateY(5px) rotate(45deg)
		}
		50% {
			transform: translateY(5px) rotate(0)
		}
		100% {
			transform: translateY(0) rotate(0)
		}
	}
	.main-header__mobile-icon-border:nth-child(2) {
		-webkit-animation: click-header-sp-bar-2-close 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards;
		animation: click-header-sp-bar-2-close 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards
	}
	@-webkit-keyframes click-header-sp-bar-2-close {
		0% {
			opacity: 0
		}
		20% {
			opacity: 0
		}
		100% {
			opacity: 1
		}
	}
	@keyframes click-header-sp-bar-2-close {
		0% {
			opacity: 0
		}
		20% {
			opacity: 0
		}
		100% {
			opacity: 1
		}
	}
	.main-header__mobile-icon-border:nth-child(3) {
		-webkit-animation: click-header-sp-bar-3-close 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards;
		animation: click-header-sp-bar-3-close 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards
	}
	@-webkit-keyframes click-header-sp-bar-3-close {
		0% {
			transform: translateY(-5px) rotate(-45deg)
		}
		50% {
			transform: translateY(-5px) rotate(0)
		}
		100% {
			transform: translateY(0) rotate(0)
		}
	}
	@keyframes click-header-sp-bar-3-close {
		0% {
			transform: translateY(-5px) rotate(-45deg)
		}
		50% {
			transform: translateY(-5px) rotate(0)
		}
		100% {
			transform: translateY(0) rotate(0)
		}
	}
	
//menu opened
	.mobile-close .main-header__mobile-icon-border:nth-child(1) {
		margin-top: -5px;
		width: 20px;
		-webkit-animation: click-header-sp-bar-1-open 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards;
		animation: click-header-sp-bar-1-open 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards
	}
	@-webkit-keyframes click-header-sp-bar-1-open {
		0% {
			transform: translateY(0) rotate(0)
		}
		50% {
			transform: translateY(5px) rotate(0)
		}
		100% {
			transform: translateY(5px) rotate(45deg)
		}
	}
	@keyframes click-header-sp-bar-1-open {
		0% {
			transform: translateY(0) rotate(0)
		}
		50% {
			transform: translateY(5px) rotate(0)
		}
		100% {
			transform: translateY(5px) rotate(45deg)
		}
	}
	.mobile-close .main-header__mobile-icon-border:nth-child(2) {
		-webkit-animation: click-header-sp-bar-2-open 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards;
		animation: click-header-sp-bar-2-open 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards
	}
	@-webkit-keyframes click-header-sp-bar-2-open {
		0% {
			opacity: 1
		}
		80% {
			opacity: 0
		}
		100% {
			opacity: 0
		}
	}
	@keyframes click-header-sp-bar-2-open {
		0% {
			opacity: 1
		}
		80% {
			opacity: 0
		}
		100% {
			opacity: 0
		}
	}
	.mobile-close .main-header__mobile-icon-border:nth-child(3) {
		margin-top: 5px;
		-webkit-animation: click-header-sp-bar-3-open 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards;
		animation: click-header-sp-bar-3-open 0.8s cubic-bezier(0.45, 0.12, 0.04, 0.96) forwards
	}
	@-webkit-keyframes click-header-sp-bar-3-open {
		0% {
			transform: translateY(0) rotate(0)
		}
		50% {
			transform: translateY(-5px) rotate(0)
		}
		100% {
			transform: translateY(-5px) rotate(-45deg)
		}
	}
	@keyframes click-header-sp-bar-3-open {
		0% {
			transform: translateY(0) rotate(0)
		}
		50% {
			transform: translateY(-5px) rotate(0)
		}
		100% {
			transform: translateY(-5px) rotate(-45deg)
		}
	}


