@use "vars" as *;

#page-P001{
	.l-content__01-l{
		@include mq(){
			margin-bottom: 20px;
		}
	}
}

.cmn-heading-block{
	position: relative;
	@include flex(between, center, wrap);
	margin-bottom: 22px;
	@include mq(){
		justify-content: center;
		background: #FAFAFA;
		padding: 30px 0;
		position: relative;
		margin-bottom: 0;
		&:after{
			content: "";
			border: solid #464646;
			border-width: 0 2px 2px 0;
			display: inline-block;
			padding: 2.5px;
			transform: rotate(-45deg) translateY(-50%);
			position: absolute;
			top: 47%;
			right: 30px;
		}
	}
	&__ttl{
		@include font-size(17);
		font-weight: 700;
		color: #3B4043;
		letter-spacing: 1px;
		@include mq(){
			text-align: center;
		}
	}
	&__link{
		@include font-size(13);
		font-weight: 500;
		color: #969696;
		padding-right: 6px;
		@include mq(){
			display: none;
		}
		&-sp{
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
			@include mq(md-min){
				display: none;
			}
		}
	}
	
	&--styles02{
		margin-bottom: 12px;
		@include mq(){
			margin: 0 calc((100vw - 100%) / 2 * -1) 18px;
			&:after{
				display: none;
			}
		}
		.cmn-heading-block__ttl{
			@include font-size(18);
			color: #464646;
		}
	}
	&.has-link{
		&:after{
			@include mq(){
				content: "";
				border: solid #464646;
				border-width: 0 2px 2px 0;
				display: inline-block;
				padding: 2.5px;
				transform: rotate(-45deg) translateY(-50%);
				position: absolute;
				top: 47%;
				right: 30px;
			}
		}
	}
}

.recent-works{
	margin-bottom: 30px;
	@include mq(){
		margin-bottom: 11px;
	}
	&__list{
		padding: 0 7px;
		&-item{
			border-bottom: 1px solid #E0E1E3;
			&:first-of-type{
				@include mq(md-min){
					border-top: 1px solid #E0E1E3;
				}
			}
		}
		&-link{
			@include flex(0, center, wrap);
			padding: 18px 10px 18px 46px;
			position: relative;
			@include fn-hover;
			@include mq(){
				padding: 16px 10px 12px 82px;
			}
			&:before{
				content: "";
				display: block;
				width: 40px;
				height: 40px;
				background: url("../img/common/img_use02.png") no-repeat center/cover;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				@include mq(){
					width: 50px;
					height: 50px;
					left: 25px;
				}
			}
		}
		&-meta{
			@include flex();
			font-weight: 700;
			background: #FCD8D8;
			border-radius: 4px;
			padding: 5px 12px;
			margin-right: 16px;
			@include mq(){
				margin-right: 0;
				margin-bottom: 3px;
			}
		}
		&-date{
			margin-right: 10px;
		}
		&-time{
			letter-spacing: 0.5px;
		}
		&-ct{
			@include flex(0, 0, wrap);
			color: #3B4043;
			@include mq(){
				padding-left: 7px;
			}
			span{
				margin-right: 10px;
				&:last-of-type{
					margin-right: 0;
				}
			}
		}
		&-user{
			background: url("../img/common/icn_user.png") no-repeat left center/16px;
			padding: 5px 0 5px 20px;
			@include mq(){
				background: url("../img/common/icn_user.png") no-repeat left center/15px;
			}
		}
		&-map{
			background: url("../img/common/icn_gps.png") no-repeat left center/11px;
			padding: 5px 0 5px 13px;
		}
		&-tag{
			position: relative;
			padding: 5px 0 5px 15px;
			&:before{
				content: "#";
				@include font-size(15);
				font-weight: bold;
				position: absolute;
				top: 12px;
				transform: translateY(-50%);
				left: 5px;
			}
		}
	}
	&__empty{
		max-width: 344px;
		width: 100%;
		height: 56px;
		border: 3px solid #E0E1E3;
		border-radius: 7px;
		@include flex(center, center, wrap);
		@include font-size(13);
		font-weight: 700;
		margin: 30px auto 0;
		color: #E0E1E3;
	}
}


.project-status{
	padding: 23px 15px;
	margin-bottom: 30px;
	@include mq(){
		margin-bottom: 4px;
	}
	&__list{
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		gap: 14px 15px;
		@include mq(tb){
			grid-template-columns: 1fr 1fr;
		}
		@include mq(){
			grid-template-columns: 1fr;
			padding: 0 5px;
		}
		&-item{
			border: 1px solid #E0E1E3;
			border-radius: 4px;
			padding: 9px 14px 16px;
			position: relative;
			@include mq(){
				padding: 27px 14px;
			}
			&:after{
				content: "";
				border: solid #464646;
				border-width: 0 2px 2px 0;
				display: inline-block;
				padding: 2.5px;
				transform: rotate(-45deg) translateY(-50%);
				position: absolute;
				top: 47%;
				right: 13px;
			}
			a{
				display: block;
				@include fn-hover;
				@include mq(){
					@include flex(0, center);
				}
			}
		}
		&-ttl{
			@include font-size(13);
			font-weight: 700;
			margin-bottom: 14px;
			@include mq(){
				width: 8em;
				@include font-size(15);
				//width: 50%;
				margin-bottom: 0;
			}
		}
		&-wp{
			@include flex(0, center, wrap);
			position: relative;
			@include mq(){
				justify-content: center;
				width: calc(100% - 9em);
				//width: 50%;
			}
		}
		&-label{
			width: 5em;
			@include font-size(13);
			font-weight: 500;
			color: #969696;
		}
		&-number{
			@include font-size(14);
			font-weight: 700;
			//position: absolute;
			//top: 50%;
			//left: 50%;
			//transform: translate(-50%, -50%);
			width: calc(100% - 7rem);
			@include mq(){
				//top: 76%;
				//left: 52%;
				width: auto;
			}
			//@include mq(sm){
			//	padding-right: 1em;
			//	width: calc(100% - 5em);
			//	text-align: right;
			//}
			span{
				@include font-size(30);
				line-height: 1;
				color: #5BC0DE;
				margin-right: 6px;
			}
		}
	}
}

.notice-block{
	margin-bottom: 23px;
	padding-bottom: 20px;
	@include mq(){
		margin-bottom: 0;
	}
	&__heading{
		margin-bottom: 22px;
		@include mq(){
			margin-bottom: 0;
		}
	}
	&__list{
		padding: 0 7px;
		@include mq(){
			//padding: 0 20px;
			padding: 0 20px;
		}
		&-item{
			border-bottom: 1px solid #E0E1E3;
			padding: 15px 24px;
			position: relative;
			@include mq(){
				padding: 8px 25px;
			}
			@include p-before(11px, 11px){
				background: #E8E8E8;
				border-radius: 50%;
				top: 32px;
				left: 6px;
				@include mq(){
					top: 27px;
					left: 4px;
				}
			}
			&:first-of-type{
				border-top: 1px solid #E0E1E3;
				@include mq(){
					border-top: none;
				}
			}
			a{
				display: block;
			}
			&.latest{
				&:before{
					background: #5BC0DE;
				}
			}
		}
		&-date{
			@include font-size(11);
			color: #707070;
			display: block;
			margin-bottom: 1px;
			letter-spacing: 0.8px;
		}
		&-ttl{
			@include fts(13, 24);
			font-weight: 500;
			color: #464646;
		}
	}
}

.todo-block{
	padding: 23px 20px;
	@include mq(){
		padding: 0;
	}
	&__heading{
		margin-bottom: 16px;
		@include mq(){
			margin-bottom: 0;
		}
	}
	&__list{
		@include mq(){
			padding: 0 20px;
		}
		&-item{
			border-bottom: 1px solid #E0E1E3;
			&:first-of-type{
				border-top: 1px solid #E0E1E3;
				@include mq(){
					border-top: none;
				}
			}
			a{
				position: relative;
				display: block;
				padding: 12px 30px;
				@include fts(13, 24);
				@include mq(){
					padding: 10px 23px;
				}
				&:before{
					content: "";
					display: inline-block;
					width: 15px;
					height: 15px;
					background: url("../img/common/icn_check.png") no-repeat left center/cover;
					position: absolute;
					top: 23px;
					left: 4px;
					transform: translateY(-50%);
					@include mq(){
						top: 14px;
						left: 3px;
						transform: translateY(0);
					}
				}
			}
		}
	}
}

.page-news-detail{
	.notice-block{
		margin-bottom: 0;
		padding-bottom: 28px;
		@include mq(){
			padding-bottom: 14px;
		}
	}
	.l-content__01-l{
		@include mq(){
			margin-bottom: 90px;
		}
	}
}

.post-detail{
	margin-bottom: 50px;
	font-weight: 500;
	@include mq(){
		margin-bottom: 30px;
	}
}