	&--03{
		.form-edit__checkbox{
			padding-left: 30px;
			.checkmark{
				width: 24px;
				height: 24px;
				border-radius: 50%;
				top: calc(50% - 12px);
				background: #FAFAFA;
				border: 1px solid #E0E1E3;
				&::after{
					width: 16px;
					height: 16px;
					border-radius: 50%;
					background: #5BC0DE;
					left: 3px;
					top: 3px;
					transform: none;
					border: none;
					box-shadow: inset -2px -2px 6px 2px rgb(0 0 0 / 18%);
				}
			}
			
			input:checked + .checkmark{
				background: #FAFAFA;
			}
		}
