				padding-right: 0;
			}
			.form-edit__checkbox{
				padding-top: 3px;
			}
		}
	}
}

.form-edit__checkbox{
	color: #464646;
	padding-left: 25px;
	font-weight: 500;
	user-select: none;
	cursor: pointer;
	position: relative;
	display: block;
	@include font-size(14);
	small{
		font-weight: 500;
		display: inline-block;
		color: #969696;
		@include font-size(12);
	}
	input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
	}
	.checkmark {
		position: absolute;
		top: -2px;
		left: 0;
		border: 1px solid #E0E1E3;
		width: 18px;
		height: 18px;
		border-radius: 50%;
		&:after {
			content: "";
			position: absolute;
			display: none;
			left: 6px;
			top: 2px;
			width: 4px;
			height: 10px;
			border: solid white;
			border-width: 0 2px 2px 0;
			transform: rotate(45deg);
		}
	}
	input:checked ~ .checkmark {
		background-color: #5BC0DE;
	}
	input:checked ~ .checkmark:after {
		display: block;
	}
	img{
		margin-right: 5px;
	}
}

.c-select-icn{
	position: relative;
	display: block;
	width: 43px;
	height: 43px;
	input[type="radio"]{
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		cursor: pointer;
		+ span{
			display: block;
			border-radius: 50%;
			overflow: hidden;
			border: 3px solid transparent;
		}
		&:checked + span{
			border: 3px solid #5BC0DE;
		}
	}
	img{
		width: 100%;
		height: 100%;
		@include object-fit(cover, center);
	}
}

