@import "vars";

.sec-achievement{
	.m-tabs{
		margin-bottom: 21px;
		@include mq(){
			margin-bottom: 28px;
		}
	}
	.m-subnav{
		@include mq(){
			margin-bottom: 0;
		}
	}
}
.achievement-block{
	@include flex(0,0,wrap);
	margin-bottom: 49px;
	@include mq(sm){
		margin-bottom: 38px;
	}
	&__l{
		width: 135px;
		@include mq(sm){
			width: 100%;
			border-bottom: 1px #E0E1E3 solid;
			padding-bottom: 26px;
			margin-bottom: 17px;
		}
	}
	&__r{
		width: calc(100% - 135px);
		padding-left: 5.3%;
		@include flex(0,0,wrap);
		@include mq(sm){
			justify-content: center;
			width: 100%;
			padding-left: 0;
			border-bottom: 1px #E0E1E3 solid;
			padding-bottom: 24px;
		}
		&--nowrapsp{
			@include mq{
				flex-wrap: nowrap;
			}
		}
	}
	&__num{
		width: 100px;
		text-align: center;
		margin-right: 10px;
		@include mq(sm){
			width: 90px;
			margin-right: 15px;
		}
		span{
			@include font-size(65);
			font-weight: 700;
			color: #FECC00;
			border-bottom: 1px #E0E1E3 solid;
			display: block;
			padding-bottom: 5px;
			margin-bottom: 7px;
		}
		small{
			@include fts(12,20);
			font-weight: 500;
			color: #969696;
		}
	}
	&__ttl{
		margin-bottom: 6px;
		@include mq(){
			margin-bottom: 28px;
		}
		span{
			margin-right: 8px;
		}
	}
	.c-example{
		a{
			text-decoration: underline;
		}
	}
}

//P044

.achievement-flex{
	@include flex(0,end,wrap);
	margin-bottom: 21px;
	@include mq(sm){
		margin: 0 -5px 30px;
	}
	&__item{
		@include flex(between,center,center);
		width: 268px;
		border-radius: 4px;
		padding: 29px 13px 22px 19px;
		border: 5px solid #F2F0EE;
		margin-right: 35px;
		@include mq(sm){
			width: 100%;
			padding: 27px 46px 22px 18px;
			margin-right: 0;
			margin-bottom: 23px;
		}
		small{
			@include font-size(14.5);
			font-weight: 700;
		}
		span{
			@include font-size(30);
			font-weight: 500;
		}
	}
	&__btn{
		@include flex(center,center,wrap);
		font-weight: 500;
		padding: 0 15px;
		border-radius: 4px;
		height: 25px;
		background: #F2F0EE;
		border: 1px #E0E1E3 solid;
		transition: 0.3s ease;
		&:hover{
			opacity: 0.7;
			transition: 0.3s ease;
		}
	}
}