@import "vars";

.work-seach{
	@include flex(0,center,wrap);
	margin-bottom: 20px;
	@include mq(){
		margin-bottom: 15px;
	}
	&__ttl{
		@include flex(0,center,wrap);
		font-weight: 500;
		color: #5BC0DE;
		margin-right: 15px;
		img{
			margin-right: 5px;
		}
	}
	&__list{
		@include flex(0,center,wrap);
		&-item{
			@include flex(0,center,wrap);
			height: 30px;
			border: 1px #78BEDB solid;
			background: rgba(91,192,222,0.19);
			border-radius: 19px;
			margin-right: 15px;
			min-width: 37px;
			padding: 0 34px 0 10px;
			position: relative;
			cursor: pointer;
			transition: 0.3s ease;
			@include p-before(1px,14px){
				background: #78BEDB;
				right: 20px;
				top: 7px;
				transform: rotate(45deg);
			}
			@include p-after(1px,14px){
				background: #78BEDB;
				right: 20px;
				top: 7px;
				transform: rotate(-45deg);
			}
			&:hover{
				opacity: 0.7;
				transition: 0.3s ease;
			}
			&.close{
				display: none;
			}
			@include mq(){
				margin-right: 10px;
			}
		}
	}
}
.work-list{
	margin-bottom: 30px;
	&__item{
		border-bottom: 1px #E0E1E3 solid;
		&:first-child{
			border-top: 1px #E0E1E3 solid;
		}
	}
	&__link{
		@include flex(0,center,wrap);
		padding: 16px 0;
		transition: 0.3s ease;
		&:hover{
			opacity: 0.7;
			transition: 0.3s ease;
		}
	}
	&__ct{
		width: calc(100% - 105px);
		@include mq(){
			width: calc(100% - 90px);
		}
	}
	&__date{
		margin-bottom: 3px;
	}
	&__meta{
		position: relative;
		padding-left: 65px;
		@include p-before(50px,50px){
			background: url(../img/common/img_use02.png) no-repeat center/contain;
			top: 0;
			left: 0;
		}
		&-name{
			@include fts(14,19);
			font-weight: 600;
			padding-top: 7px;
		}
		&-date{
			@include flex(0,center,wrap);
			span{
				@include flex(0,center,wrap);
				@include font-size(12);
				font-weight: 500;
				margin-right: 10px;
				img{
					margin-right: 2px;
				}
			}
		}
	}
	&__btn{
		width: 105px;
		padding-top: 5px;
		@include mq(){
			width: 90px;
		}
		span{
			@include flex(center,center,wrap);
			width: 90px;
			height: 25px;
			border: 1px #7DD8C9 solid;
			border-radius: 15px;
			font-weight: 700;
			color: #7DD8C9;
		}
		&--red{
			span{
				color: #F04850;
				border-color: #F04850;
			}
		}
		&--green{
			span{
				color: #fff;
				background: #7DD8C9;
			}
		}
		&--black{
			span{
				color: #969696;
				border-color: #969696;
			}
		}
		&--black02{
			span{
				background: #464646;
				color: #fff;
				border-color: #464646;
			}
		}
	}
	&__ttl{
		color: #464646;
		font-weight: bold;
		@include font-size(14);
		span{
			display: block;
			font-weight: 500;
			margin-top: 1px;
			@include font-size(12);
			span{
				display: inline-block;
				color: #F04850;
			}
		}
	}
}

