@use "vars" as *;
.sec-function{
	padding-top: 11px;
	padding-bottom: 11px;
	@include mq{
		padding: 0;
	}
}
.function-block{
	text-align: center;
	max-width: 960px;
	padding: 40px 40px 69px;
	background: #ffffff;
	margin: 0 auto;
	@include mq{
		padding: 15px 20px 0;
	}
	
}
.function-tabs{
	@include flex(between, 0);
	max-width: 414px;
	margin: 0 auto 42px;
	position: relative;
	@include mq{
		max-width: 285px;
		margin-bottom: 33px;
	}
	&:after{
		content: '';
		width: calc(100% - 65px);
		height: 2px;
		background: #5BC0DE;
		position: absolute;
		left: 32.5px;
		right: 32.5px;
		top: 32px;
		z-index: 0;
		@include mq{
			top: 22px;
		}
		
	}
	&__item{
		text-align: center;
		position: relative;
		z-index: 2;
		&.active{
			.function-tabs{
				&__img{
					background: #5BC0DE;
					&-white{
						display: block;
					}
					&-normal{
						display: none;
					}
				}
				&__txt{
					color: #5BC0DE;
				}
			}
		}
	}
	&__img{
		width: 65px;
		height: 65px;
		border-radius: 100%;
		border: 1px solid #5BC0DE;
		@include flex(center, center);
		background: #ffffff;
		display: inline-flex;
		margin-bottom: 7px;
		@include mq{
			width: 44px;
			height: 44px;
			margin-bottom: 1px;
		}
		img{
			width: 36px;
			@include mq{
				width: 25px;
			}
		}
		&-normal{
			display: block;
		}
		&-white{
			display: none;
		}
	}
	&__txt{
		@include fts(14, 29);
		font-weight: 500;
		color: #464646;
		display: block;
		@include mq{
			@include font-size(11);
		}
	}
}
.function-ct{
	.c-txt__01{
		@include mq(sm){
			line-height: 1.428;
			text-align: left;
		}
	}
}
.function-form{
	text-align: left;
	max-width: 400px;
	margin: 0 auto;
	&__group{
		margin-bottom: 20px;
		@include mq{
			margin-bottom: 26px;
		}
		&:last-child{
			margin-bottom: 0;
		}
		&-td{
			display: inline-flex;
			margin-bottom: 7px;
			@include mq{
				margin-bottom: 11px;
			}
		}
		&-dd{
			
			& > a{
				color: #464646;
				text-decoration: none;
				margin-left: 5px;
				@include mq{
					margin-left: 11px;
				}
			}
		}
	}
	& + .c-control--submit{
		max-width: 401px;
		gap: 17px;
	}
	.btn_back{
		color: #969696;
		text-decoration: none;
	}
}