@use "vars" as *;


.message-box{
	margin-bottom: 35px;
	@include mq(){
		margin: 0 calc(((100vw - 100%) / 2) * -1) 15px;
	}
	&__item{
		margin-bottom: 22px;
		position: relative;
		@include mq(){
			background: rgba(#FED3CF, 0.28);
			margin-bottom: 22px;
			@include p-before(100%, 27px){
				background: rgba(#FED3CF, 0.28);
				top: 0;
				left: 0;
				transform: translateY(-100%);
			}
		}
		&:last-of-type{
			margin-bottom: 0;
		}
		&.viewed{
			&:before{
				display: none;
			}
			@include mq(){
				.message-box__heading{
					background: #fff;
				}
			}
			.message-box__cnt{
				background: #fff;
				border-color: #E0E1E3;
			}
		}
	}
	&__heading{
		@include flex(between, center);
		@include mq(){
			padding: 0 15px 12px;
			//background: rgba(#FED3CF, 0.28);
		}
		&-left{
			@include flex(0, center);
		}
		&-txt{
			@include font-size(15);
			font-weight: 700;
			padding-left: 12px;
		}
		&-right{
			@include font-size(12);
			position: relative;
			top: 7px;
			@include mq(){
				top: 15px;
			}
		}
		&-status{
			color: #5BC0DE;
		}
		&-time{
			margin-left: 10px;
		}
	}
	&__cnt{
		width: 90.17%;
		margin-left: auto;
		margin-right: 0;
		@include fts(13, 25);
		padding: 10px 16px 20px;
		border: 1px solid rgba(#FED3CF, 0.28);
		border-radius: 10px;
		background: rgba(#FED3CF, 0.28);
		@include mq(){
			border: none;
			background: transparent;
			width: 100%;
			padding: 0 42px 36px;
		}
	}
	
	&__date{
		text-align: center;
		margin-bottom: 15px;
		position: relative;
		span{
			@include font-size(12);
			display: inline-block;
			padding: 6px 16px;
			border-radius: 50px;
			border: 1px solid #969696;
			background: #fff;
			position: relative;
			z-index: 2;
			min-width: 136px;
		}
		@include p-after(100%, 1px){
			background: #969696;
			top: 50%;
			left: 0;
		}
	}
	
	
	&__other{
		@include mq(){
			padding: 0 15px;
		}
		&-heading{
			@include font-size(12);
			text-align: right;
			margin-bottom: 10px;
		}
		&-wp{
			padding: 15px 20px 10px;
			background: #FCF6D2;
			border-radius: 10px;
			position: relative;
			z-index: 3;
			@include flex(0, center);
			width: 98.8%;
			margin-left: auto;
			margin-right: 0;
			@include mq(){
				width: 100%;
				padding: 14px 20px;
			}
		}
		
		&-img{
			width: 50px;
		}
		&-ct{
			width: calc(100% - 50px);
			padding-left: 13px;
		}
		&-ttl{
			@include font-size(18);
			font-weight: 700;
			color: #E57200;
			margin-bottom: 5px;
			@include mq(){
				@include font-size(16);
				margin-bottom: 10px;
			}
		}
		&-desc{
			@include font-size(12);
			font-weight: 500;
			color: #464646;
			line-height: 1.58;
			a{
				color: #5BC0DE;
				text-decoration: underline;
			}
		}
	}
}

.message-form{
	@include mq(){
		height: 130px;
		position: relative;
		z-index: 10;
	}
	&__wp{
		width: 97.2%;
		margin-left: auto;
		position: relative;
		@include mq(){
			width: 100%;
			position: fixed;
			left: 0;
			right: 0;
			bottom: 0;
			background: #fff;
			z-index: 10;
			box-shadow: 0 0 16px 3px rgba(#000, 0.16);
		}
	}
	&__textarea{
		width: 100%;
		height: 166px;
		border: 1px solid #E0E1E3;
		border-radius: 4px;
		background: #fff;
		padding: 18px 14px;
		@include font-size(14);
		font-weight: 500;
		@include mq(){
			height: 70px;
			padding: 12px 16px;
		}
	}
	&__control{
		@include flex(between, center);
		padding-top: 15px;
		@include mq(){
			padding: 10px 16px;
		}
	}
	&__file{
		width: 25px;
		height: 28px;
		overflow: hidden;
		position: relative;
		cursor: pointer;
		top: -6px;
		@include mq(){
			width: 17px;
			height: 19px;
		}
		@include p-before(25px, 28px){
			background: url("../img/common/icn_file.png") no-repeat center/contain;
			@include smooth-transition;
			@include mq(){
				width: 17px;
				height: 19px;
			}
		}
		input{
			height: 25px;
			opacity: 0;
			cursor: pointer;
		}
		&:hover{
			&:before{
				background: url("../img/common/icn_file_hover.png") no-repeat center/contain;
				@include smooth-transition;
			}
		}
	}
	&__submit{
		width: 160px;
		@include mq(){
			width: 77px;
		}
		.btn_entry{
			height: 40px;
			padding: 0.8em;
			@include mq(){
				height: 30px;
				padding: 0.5em 0;
			}
		}
	}
}

::placeholder {
	color: #969696;
	opacity: 1;
}

:-ms-input-placeholder {
	color: #969696;
}

::-ms-input-placeholder {
	color: #969696;
}

.user-request{
	margin-bottom: 20px;
}

.works-user{
	margin-bottom: 15px;
	&:last-of-type{
		margin-bottom: 0;
	}
	&__wp{
		border-radius: 4px;
		padding: 10px 20px 28px;
		background: #fafafa;
	}
	&__heading{
		@include flex(center, center);
		margin-bottom: 17px;
		&-number{
			@include flex(center, center);
			width: 30px;
			height: 30px;
			border: 1px solid #5BC0DF;
			background: #fff;
			border-radius: 50%;
			margin-right: 10px;
			@include font-size(13);
			font-weight: 700;
			color: #5BC0DF;
		}
		&-txt{
			@include font-size(14);
			font-weight: 500;
		}
	}
	
	&__ct{
		@include mq(){
			padding: 0 17px;
		}
	}
	
	&__item{
		@include flex(between, center);
		@include font-size(13);
		font-weight: 500;
		margin-bottom: 24px;
		&:last-of-type{
			margin-bottom: 0;
		}
		&-label{
			font-weight: 700;
		}
		&-desc{
			text-align: right;
			a{
				font-weight: 700;
				display: inline-block;
				text-decoration: underline;
			}
		}
	}
}

