.tb-transfer{
	width: 100%;
	margin-bottom: 18px;
	th, td{
		padding: 8.5px 10px;
		text-align: center;
		border: 1px solid #E0E1E3;
		&:first-child{
			width: 30.5%;
		}
		&:nth-child(2n){
			width: 36.666%;
		}
		&:nth-child(3n){
			width: 32.82%;
		}
	}
	th{
		background: #F2F0EE;
		font-weight: 600;
	}
	td{
		font-weight: 500;
	}
}
.quote-list--start{
	.quote-list__fee-item{
		align-items: flex-start;
		
	}
	
}
#page-P039{
	
	.quote-list__item{
		margin-bottom: 11px;
		padding-bottom: 21px;
		&:last-child{
			margin-bottom: 32px;
			padding-bottom: 17px;
		}
	}
	.c-control__02{
		margin-top: 10px;
	}
}
