@use "vars" as *;

.message-list{
	margin-bottom: 25px;
	@include mq(){
		margin: 0 calc(((100vw - 100%) / 2) * -1) 32px;
	}
	&__item{
		border-bottom: 1px solid #E0E1E3;
		&:first-of-type{
			border-top: 1px solid #E0E1E3;
		}
	}
	&__img{
		position: absolute;
		top: calc(50% - 35px);
		left: 10px;
		width: 50px;
		height: 50px;
		border-radius: 50%;
		overflow: hidden;
		img{
			width: 100%;
			height: 100%;
			@include object-fit(cover);
		}
	}
	&__link{
		position: relative;
		padding: 20px 65px 15px 70px;
		display: block;
		@include mq(){
			padding: 20px 63px 20px 88px;
		}
		&:hover{
			background: rgba(#5BC0DE, 0.1);
		}
	}
	&__meta{
		@include flex(between, center);
		margin-bottom: 5px;
		&-name{
			@include font-size(15);
			font-weight: 700;
			@include mq(){
				@include font-size(14);
			}
		}
		&-date{
			@include font-size(12);
		}
	}
	&__desc {
		@include fts(13, 24);
		font-weight: 500;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-line-clamp: 2;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		@include mq() {
			@include fts(12, 17);
		}
	}
	&__qty{
		@include font-size(13);
		@include flex(center, center);
		width: 25px;
		height: 25px;
		border-radius: 50%;
		background: #F04850;
		color: #fff;
		position: absolute;
		top: 50%;
		right: 20px;
		@include mq(){
			right: 15px;
		}
	}
}


.content-load-more li{
	display: none;
}