@import "vars";

#page-P047{
	.form-fileimg {
		max-width: 498px;
		width: 100%;
		@include mq(){
			max-width: calc(100% + 16px);
			width: calc(100% + 16px);
			margin: 0 -8px;
		}
	}
	.c-txt__04{
		font-weight: 500;
	}
	.request-form--02{
		//margin: 0 0 37px;
		.request-form__item {
			padding-top: 18px;
			padding-bottom: 16px;
			//border-bottom: none;
		}
	}
	@include mq(){
		.request-form__ttl {
			margin-bottom: 5px;
		}
	}
	.w100{
		width: 100%;
		padding: 0;
	}
}

#page-P047,
#page-P047-2,
#page-P047-3{
	.input-samerow{
		margin: 0;
	}
	.request-form--02 .hr{
		border-top: 0;
	}
}


.form-check--03{
	margin-bottom: 44px;
	.form-check__item {
		margin-bottom: 22px;
	}
	.form-edit__checkbox {
		padding-left: 34px;
		padding-top: 4px;
	}
}


.input-samerow{
	@include flex();
	margin: 0 -8px;
	&__item{
		width: 50%;
		padding: 0 4px;
	}
}


.form_part_select{
	width: 245px;
	margin-bottom: 26px;
	@include mq(){
		width: 91%;
		margin-bottom: 28px;
	}
}

.list-file{
	margin-bottom: 10px;
	&__item{
		margin-bottom: 16px;
		@include mq(){
			margin-bottom: 11px;
		}
		&:last-child{
			margin-bottom: 0;
		}
		.form-fileimg label {
			padding: 23px 15px 31px;
		}
	}
 }

 .support-ct__form{
	&-item{
		padding-bottom: 9px;
		margin-bottom: 9px;
		border-bottom: 1px solid #E0E1E3;
		@include flex();
	}
	&-flex{
		@include flex();
		padding-bottom: 17px;
		@include mq(){
			@include flex(0,0,wrap);
			padding-bottom: 0;
		}
	}
	&-img{
		width: 155px;
		box-sizing: border-box;
		overflow: hidden;
		@include mq(){
			width: 100%;
			max-width: 46.333%;
			margin: 0 auto 6px;
		}
		.type-video{
			display: none;
		}
		&.on{
			.type-video{
				display: block;
			}
			.type-img{
				display: none;
			}
		}
		img{
			width: 100%;
			padding-bottom: 9px;
		}

		.c-btn__07{
			margin-left: auto;
		}
	}
	&-file{
		width: calc(100% - 155px);
		padding-left: 14px;
		box-sizing: border-box;
		@include mq(){
			padding-left: 0;
			width: 100%;
		}
		label{
			cursor: pointer;
		    padding: 20px 20px 22px;
			box-sizing: border-box;
			margin-bottom: 7px;
			background: #FAFAFA;
			text-align: center;
			color: #969696;
			font-weight: 500;
			position: relative;
			text-align: center;
			color: #969696;
			line-height: 1.428;
			display: block;
			@include font-size(14);
			transition: all 0.3s;
			@include mq(){
				padding: 25px 20px 22px;
				margin: 0 7px;
			}
			@include mq(xs){
				margin: 0;
			}
			&:hover{
				opacity: 0.7;
				transition: all 0.3s;
			}
			span{
				text-decoration: underline !important;
			}
			&::before{
				content: '';
				height: calc(100% - 22px);
				width: calc(100% - 22px);
				border: 1px dashed #707070;
				top: 10px;
				left: 10px;
				position: absolute;
			}
		}
		input[type=file]{
			opacity: 0;
			position: absolute;
			z-index: -1;
			width: 0;
		}
	}
	&-desc{
		color: #969696;
		line-height: 1.538;
		padding-left: 1px;
		font-weight: 500;
		@include font-size(13);
		@include mq(){
			padding-top: 10px;
			padding-bottom: 19px;
		}
		.type-video{
			display: none;
		}
		&.on{
			.type-video{
				display: block;
			}
			.type-img{
				display: none;
			}
		}
		span{
			color: #464646;
			display: block;
			padding-bottom: 10px;
			line-height: 1.9;
			@include font-size(14);
		}
		small{
			display: inline-block;
			color: #5BC0DE;
			font-weight: bold;
			position: relative;
			@include font-size(14.5);
			@include mq(){
				text-align: center;
			}
		}
	}
	&-date{
		font-weight: 500;
		color: #464646;
		@include font-size(14);
		span{
			color: #969696;
			margin-left: 40px;
			@include font-size(13);
		}
	}
}

.file-option{
	.item{
		display: none;
		&.active{
			display: block;
		}
		&.show{
			display: block;
			//border-bottom: 1px solid #E0E1E3;
			//padding-bottom: 20px;
			//margin-bottom: 37px;
			@include mq(){
				padding-bottom: 33px;
				margin-bottom: 25px;
			}
		}
	}
}

.quote-list__item02--02{
	@include mq(){
		.quote-list__ttl{
			text-align: center;
		}
	}
}

.support-ct__form-flex--file{
	.c-btn__07--red{
		display: none;
	}
	&.on{
		.c-btn__07--red{
			display: block;
		}
	}
}