@use "vars" as *;

#upload-photo01,#imgInp,#upload-photo02,#upload-photo03,#upload-photo04,#upload-photo05,#upload-photo06,#upload-photo07,#upload-photo08,#upload-photo09,#upload-photo10 {
	opacity: 0;
	position: absolute;
	z-index: -1;
 }

 #page-P029{
	.job-infor{
		padding-bottom: 31px;
		margin-bottom: 38px;
		border-bottom: 1px solid #E0E1E3;
		@include mq(){
			padding-bottom: 42px;
			margin-bottom: 41px;
			&:last-of-type{
				padding-bottom: 33px;
				margin-bottom: 29px;
			}
		}
		&--borderno{
			border-bottom: 0;
			padding-bottom: 10px;
		}
	}
	.job-infor__ttl {
		margin-bottom: 15px;
		@include mq(){
			margin-bottom: 12px;
		}
	}
 }

.form-block{
	counter-reset: number;
}
.js-add-input-item .js-number:before{
	content:"0";
}
.js-add-input-item02 .js-number02:before{
	content:"0";
}
.js-add-file{
	display: none;
}
.js-add-file.on{
	display: inline-block;
}

.js-add-file02{
	display: none;
}
.js-add-file02.on{
	display: inline-block;
}

//css #page-P030
#page-P030{
	.quote-list{
		margin-top: 26px;
	}
}

.work-list--02{
	margin-bottom: 34px;
	.work-list__link{
		padding: 13px 0 16.5px;
		&:hover{
			opacity: 1;
		}
	}
	.work-list__btn{
		@include flex(end);
		margin-top: 0;
		span{
			width: 63px;
		}
	}
}

.js-ct-acc{
	display: none;
}

.js-check-form{
	display: none;
	&.show{
		display: block;
	}
}

.js-show-acc{
	.off{
		display: none;
	}
	&.on{
		&::before{
			background: url('/assets/img/p029/icon-hide.png') center no-repeat;
			background-size: contain;	
		}
		.on{
			display: none;
		}
		.off{
			display: block;
		}
	}
}

.request-form--02{
	@include mq(md-min){
		.form-check{
			@include flex(0,0,wrap);
			&__item{
				margin-right: 35px;
			}
			&--full{
				display: block;
				padding-top: 7px;
				padding-left: 3px;
			}
		}
		.request-form__ttl{
			width: 176px;
			padding: 0;
		}
		.request-form__ct{
			padding-right: 0;
			width: calc(100% - 176px);
		}
	}
	// .form_part_select select{
	// 	height: 38px;
	// }
	// .form-edit__input{
	// 	height: 38px;
	// }
}

//css p031
.job-infor__flex{
	@include flex();
	padding-right: 14px;
	@include mq(xl){
		padding-right: 0;
	}
	@include mq(){
		@include flex(0,0,wrap);
	}
	&-ct{
		padding-right: 58px;
		width: calc(100% - 233px);
		@include mq(xl){
			padding-right: 25px;
		}
		@include mq(){
			padding-right: 0;
			width: 100%;
			margin-bottom: -7px;
		}
		small{
			line-height: 1.857;
			@include mq(){
				margin-top: 0;
			}
		}
		p{
			margin-top: 8px;
		}
	}
	&-img{
		width: 233px;
		margin-top: -8px;
		@include mq(){
			width: 100%;
			margin-top: 0;
			text-align: center;
		}
		img{
			max-width: 100%;
			@include mq(){
				max-width: 233px;
			}
		}
	}
	&--02{
		@include mq(md-min){
			padding-right: 0;
			.job-infor__flex-ct{
				padding-right: 26px;
				width: calc(100% - 312px);
			}
			.job-infor__flex-img{
				width: 312px;
			}
		}
		.job-infor__flex-img{
			img{
				@include mq(){
					max-width: 312px;
				}
			}
		}
	}
	&--03{
		@include mq(md-min){
			padding-right: 0;
			.job-infor__flex-ct{
				padding-right: 26px;
				width: calc(100% - 312px);
			}
			.job-infor__flex-img{
				width: 312px;
				text-align: right;
				padding-right: 31px;
				margin-top: -16px;
			}
		}
	}
	&--04{
		@include mq(md-min){
			padding-right: 19px;
			.job-infor__flex-ct{
				width: calc(100% - 189px);
			}
			.job-infor__flex-img{
				width: 189px;
			}
			
		}
	}
}


//css P041
.work-list--03{
	margin-bottom: 41px;
	@include mq(){
		margin-bottom: 32px;
	}
	.work-list__item{
		padding: 17px 0 19px;
		@include flex();
		@include mq(){
			padding: 15px 0 17px;
			@include flex(0,0,wrap);
		}
	}
	.work-list__ct{
		width: 209px;
		padding-right: 15px;
		@include mq(){
			width: 100%;
			padding-right: 0;
			margin-bottom: 13px;
		}
	}
	.c-control__02{
		width: calc(100% - 209px);
		margin-top: 8px;
		@include mq(){
			width: 100%;
			margin-top: 0;
		}
		.form-edit__radio{
			span{
				font-weight: 500;
				min-width: 106px;
				padding-top: 6px;
				padding-bottom: 6px;
			}
		}
	}
}

.form-edit__input-show{
	display: none;
	&.show{
		display: block;
	}
}

.job-infor__file--check{
	.job-infor__file-link{
		display: none;
	}
	&.on{
		.job-infor__file-link{
			display: block;
		}
		.job-infor__file-img:after{
			display: none;
		}
	}
}