@use "vars" as *;

.page-account-setting{
	.c-ttl__02{
		@include mq(){
			text-align: center;
			margin-bottom: 20px;
		}
	}
}

.support-ct{
	text-align: left;
	width: 100%;
	display: block;
	&__table{
		margin-bottom: 40px;
		&-item{
			@include flex(0,start);
			padding: 25px 0 20px;
			border-bottom: 1px solid #E0E1E3;
			@include mq(){
				display: block;
			}
			&:first-of-type{
				border-top: 1px solid #E0E1E3;
			}
		}
		&-ttl{
			box-sizing: border-box;
			width: 222px;
			color: #464646;
			font-weight: bold;
			padding: 5px 15px 5px 45px;
			cursor: pointer;
			transition: all 0.3s;
			@include font-size(14);
			position: relative;
			@include mq(){
				width: 100%;
				margin-bottom: 30px;
			}
			&:hover{
				transition: all 0.3s;
				opacity: 0.7;
			}
			&::before{
				content: '';
				width: 29px;
				height: 29px;
				border-radius: 4px;
				background: #F2F0EE;
				border: 1px solid #DDDDDD;
				position: absolute;
				left: 0;
				top: -4px;
			}
			&::after{
				content: "";
				width: 6px;
				height: 6px;
				border-right: 2px solid #464646;
				border-bottom: 2px solid #464646;
				position: absolute;
				left: 11px;
				top: 8px;
				transform: rotate(-135deg);
			}
			&--off{
				&::after{
					top: 6px;
				transform: rotate(45deg);
				}
			}
		}
		&-ct{
			padding-top: 6px;
			width: calc(100% - 222px);
			@include mq(){
				width: 100%;
				padding-top: 0;
			}
		}
		&-title{
			color: #464646;
			font-weight: bold;
			padding-top: 11px;
			display: block;
			margin-bottom: 20px;
			@include font-size(14);
		}
		&-accordion{
			margin-top: 15px;
			display: none;
			&.show{
				display: block;
			}
			// &.on{
			// 	.form-edit__checkbox .checkmark{
			// 		background-color: #5BC0DE;
			// 		&:after {
			// 			display: block;
			// 		}
			// 	}
			// }
		}
	}
}

.list-checkbox{
	@include flex(0,0,wrap);
	&__item{
		min-width: 93px;
		padding-right: 10px;
		margin-bottom: 12.5px;
		&:last-child{
			margin-bottom: 0;
		}
		&--full{
			width: 100%;
			margin-bottom: -15px;
		}
	}
}

.form-edit__checkbox{
	color: #464646;
	font-weight: 500;
	@include font-size(14);
	display: block;
	position: relative;
	padding-left: 30px;
	cursor: pointer;
	user-select: none;
	input {
		position: absolute;
		opacity: 0;
		cursor: pointer;
		height: 0;
		width: 0;
	}
	.checkmark {
		position: absolute;
		top: -5px;
		left: 0;
		height: 20px;
		width: 20px;
		box-sizing: border-box;
		background-color: #FAFAFA;
		border-radius: 4px;
		border: 1px solid #E0E1E3;
		&:after {
			content: "";
			position: absolute;
			display: none;
			left: 6px;
			top: 1px;
			width: 6px;
			height: 12px;
			border: solid white;
			border-width: 0 2px 2px 0;
			transform: rotate(45deg);
		}
	}
	input:checked ~ .checkmark {
		background-color: #5BC0DE;
	}
	input:checked ~ .checkmark:after {
		display: block;
	}
	&--02{
		.checkmark{
			width: 22px;
			height: 22px;
			top: 0;
			&:after{
				width: 5px;
				border-width: 0 3px 3px 0;
			}
		}
	}
}