@use "vars" as *;

/* ====================================================
Footer
==================================================== */
.btn-fix-contact{
	width: 200px;
	height: 60px;
	color: #fff;
	@include font-size(14);
	font-weight: 500;
	line-height: 60px;
	text-align: center;
	background: $color-rd;
	border-radius: 10px 0 0 0;
	transition: .5s;
	z-index: 100;
	i{
		margin-right: 10px;
	}
	@include mq(md-min){
		display: none!important;
	}
	@include mq(){
		position: fixed;
		bottom: 0;
		right: 0;
		opacity: 0;
		visibility: hidden;
		&.is-show{
			opacity: 1;
			visibility: visible;
		}
		&.absolute{
			opacity: 0;
			visibility: hidden;
			//top: -60px;
			//bottom: auto;
			//position: absolute;
		}
	}
}


.main-footer{
	position: relative;
	padding: 40px 0 0;
	background: rgb(132,226,238);
	background: linear-gradient(90deg, rgba(132,226,238,1) 0%, rgba(255,255,130,1) 100%);;
	@include mq(){
		padding: 0;
		//margin-top: 60px;
	}
	.inner{
		@include mq(){
			padding: 0;
		}
	}
	&__content{
		@include flex(between, start, wrap);
		margin-bottom: 38px;
		@include mq(){
			display: block;
			margin-bottom: 0;
		}
	}
	&__nav{
		&-item{
			margin-bottom: 38px;
			@include mq(){
				margin-bottom: 0;
				border-bottom: 1px solid rgba(#707070, 0.5);
			}
			&:last-of-type{
				margin-bottom: 0;
			}
		}
		&-ttl{
			@include font-size(16);
			font-weight: 700;
			margin-bottom: 10px;
			position: relative;
			@include mq(){
				color: #fff;
				padding: 12px 15px;
				background: rgba(#969696, 0.5);
				margin-bottom: 0;
				&:after{
					content: "";
					display: inline-block;
					border: solid #fff;
					border-width: 0 2px 2px 0;
					padding: 2.5px;
					transform: rotate(45deg) translateY(-50%);
					position: absolute;
					top: 50%;
					right: 24px;
					@include smooth-transition;
				}
			}
			&.is-close{
				&:after{
					transform: rotate(-135deg) translateY(-50%);
					top: 40%;
				}
			}
		}
		
		&-ct{
			@include mq(){
				display: none;
			}
		}
	}
	&__menu{
		@include flex(0, 0, wrap);
		@include mq(){
			display: block;
		}
		&-item{
			padding-right: 44px;
			margin-bottom: 10px;
			position: relative;
			@include mq(){
				width: 100%;
				margin-bottom: 0;
				border-bottom: 1px solid rgba(#707070, 0.5);
				padding: 13.5px 35px;
			}
			&:after{
				content: "｜";
				display: inline-block;
				@include font-size(15);
				font-weight: 500;
				position: absolute;
				top: 0;
				right: 15px;
				@include mq(){
					display: none;
				}
			}
			&:last-of-type{
				padding-right: 0;
				@include mq(){
					border-bottom: none;
				}
				&:after{
					display: none;
				}
			}
			a{
				display: block;
			}
		}
	}
	&__link{
		@include font-size(14.5);
		font-weight: 500;
	}
	&__copyright{
		padding: 9px 0;
		background: rgba(#969696, 0.5);
		@include mq(){
			padding: 20px 0 13px;
		}
		.inner{
			align-items: center;
			justify-content: space-between;
			padding: 0 25px 0 40px;
			@include mq(){
				padding: 0 15px;
			}
		}
		&-left{
			@include font-size(14.5);
			color: #fff;
			font-weight: 300;
			@include mq(){
				order: 2;
				width: 100%;
				text-align: center;
			}
		}
		&-right{
			@include mq(){
				order: 1;
				width: 100%;
			}
		}
		&--pdr{
			padding-right: 82px;
		}
	}
	
	&__social{
		@include flex(0, center);
		@include mq(){
			justify-content: center;
			margin-bottom: 14px;
		}
		&-item{
			margin-left: 33px;
			@include mq(){
				margin: 0 17px;
			}
		}
	}
}


.main-footer02{
	padding: 20px 0;
	.inner{
		@include mq(md-min){
			@include flex(between, center);
		}
		@include mq(){
			text-align: center;
		}
	}
	&__copyright{
		color: #969696;
		@include font-size(13);
		@include mq(){
			margin-top: 1em;
			@include font-size(12);
		}
	}
}